r: transparent;\n$tl-text-muted-color: #C3C2C5;\n$tl-disabled-color: #9E9E9E;\n$tl-border-color: rgba(#eee, 0.2);\n$tl-card-bg-color: rgba($black, 0.12);\n\n$bg-glass-hibiscus: linear-gradient(\n  to right bottom,\n  #f05f57,\n  #c83d5c,\n  #99245a,\n  #671351,\n  #360940\n);\n$bg-glass-purple-pizzazz: linear-gradient(\n  to right bottom,\n  #662d86,\n  #8b2a8a,\n  #ae2389,\n  #cf1d83,\n  #ed1e79\n);\n$bg-glass-blue-lagoon: linear-gradient(\n  to right bottom,\n  #144e68,\n  #006d83,\n  #008d92,\n  #00ad91,\n  #57ca85\n);\n$bg-glass-electric-violet: linear-gradient(\n  to left top,\n  #4a00e0,\n  #600de0,\n  #7119e1,\n  #8023e1,\n  #8e2de2\n);\n$bg-glass-portage: linear-gradient(\n  to left top,\n  #97abff,\n  #798ce5,\n  #5b6ecb,\n  #3b51b1,\n  #123597\n);\n$bg-glass-tundora: linear-gradient(\n  to left top,\n  #ef4673,\n  #ffc846\n);\n",".collapse-icon {\n  .card-header {\n    position: relative;\n  }\n\n  a[data-toggle=\"collapse\"] {\n    &:after {\n      position: absolute;\n      top: 40%;\n      right: 20px;\n      margin-top: -4px;\n      font-family: 'feather';\n      content: \"\\e897\";\n      transition: all 300ms linear 0s;\n    }\n\n    &:before {\n      position: absolute;\n      top: 40%;\n      right: 20px;\n      margin-top: -4px;\n      font-family: 'feather';\n      content: \"\\e8b1\";\n      transition: all 300ms linear 0s;\n    }\n  }\n\n  &.left {\n    a {\n      padding-left: 28px;\n\n      &:before,\n      &:after {\n        left: 20px;\n        right: auto;\n      }\n    }\n  }\n\n  a[aria-expanded=\"true\"] {\n    &:before {\n      opacity: 0;\n    }\n  }\n\n  a[aria-expanded=\"false\"] {\n    &:after {\n      opacity: 0;\n    }\n  }\n\n  &.accordion-icon-rotate {\n    a[aria-expanded=\"true\"] {\n      &:before {\n        transform: rotate(180deg);\n      }\n    }\n\n    a[aria-expanded=\"false\"] {\n      &:after {\n        transform: rotate(-180deg);\n      }\n    }\n  }\n}\n","// basic badge\n.badge{\n\tletter-spacing: 0.3px;\n\tbackground-color: $primary;\n\tcolor: $white;\n\n\t&.badge-light {\n\t\tcolor: $body-color;\n\t}\n\n\t// round badge\n\t&.badge-round {\n\t\tpadding: 6px 8px;\n\t}\n}\n","// buttons basic and outline\n.btn {\n  box-shadow: none;\n  &:focus,\n  &.focus,\n  &:active {\n    box-shadow: none !important;\n  }\n  &:hover {\n    box-shadow: 0 2px 7px rgba($black, 0.28);\n    cursor: pointer;\n  }\n}\n// warning button\n.btn-warning {\n  color: $white;\n}\n// btn-light\n.btn-light {\n  color: $body-color;\n}\n\n// vertical buttons\n.btn-group-vertical {\n  .btn {\n    &[class*=\"btn-\"] {\n      margin-bottom: 0;\n    }\n  }\n}\n\n// input-group button\n.input-group {\n  margin-bottom: 1rem;\n  .btn {\n    &[class*=\"btn-\"] {\n      margin-bottom: 0;\n    }\n  }\n}\n\n// round icon buttons\n.btn-icon {\n  &.round {\n    padding: 0.465rem 0.72rem;\n  }\n}\n\n// button active\n.btn[class*=\"btn-\"]:not(:disabled):not(.disabled):active,\n.btn[class*=\"btn-\"]:not(:disabled):not(.disabled).active,\n.show > .btn[class*=\"btn-\"].dropdown-toggle {\n  // color: inherit;\n  box-shadow: none;\n}\n\n// button focus\nbutton {\n  &:focus {\n    outline: none;\n  }\n}\n\n// Buttons\n.btn-group, .input-group {\n  span{\n    &:first-child:not(:last-child) {\n      .btn.dropdown-toggle{\n        border-top-right-radius: 0;\n        border-bottom-right-radius: 0;\n      }\n    }\n    &:last-child:not(:first-child) {\n      .btn.dropdown-toggle{\n        border-top-left-radius: 0;\n        border-bottom-left-radius: 0;\n      }\n    }\n    &:not(:last-child):not(:first-child) {\n      .btn.dropdown-toggle{\n        border-radius: 0;\n      }\n    }\n  }\n}",".card {\n  margin: 15px 0;\n  @include card-shadow();\n\n  &[class*=\"gradient-\"] {\n    .card-header,\n    .card-footer {\n      background-color: initial;\n    }\n  }\n\n  .card-header {\n    padding-bottom: 0;\n    border-bottom: none;\n    background-color: transparent;\n  }\n\n  .card-subtitle {\n    margin-top: 10px;\n    margin-bottom: 10px;\n  }\n\n  .card-bordered {\n    border: 1px solid rgba($black, 0.06);\n  }\n\n  &.card-transparent {\n    background-color: transparent !important;\n  }\n\n  .card {\n    box-shadow: none;\n  }\n\n  .card-img {\n    position: relative;\n\n    .card-title {\n      color: $white;\n      position: absolute;\n      bottom: 0;\n      left: 0;\n      max-width: 100%;\n      padding: 24px;\n    }\n\n    &.overlap {\n      margin-top: -30px;\n    }\n  }\n\n  .btn-floating {\n    display: inline-block;\n    color: $white;\n    position: relative;\n    overflow: hidden;\n    z-index: 1;\n    width: 40px;\n    height: 40px;\n    line-height: 40px;\n    padding: 0;\n    background-color: theme-color(\"primary\");\n    border-radius: 50%;\n    transition: 0.3s;\n    cursor: pointer;\n    vertical-align: middle;\n    font-size: 1rem;\n    box-shadow: 0 2px 2px 0 rgba($black, 0.14), 0 1px 5px 0 rgba($black, 0.12),\n      0 3px 1px -2px rgba($black, 0.2);\n\n    &.btn {\n      margin: 0;\n    }\n\n    i {\n      width: inherit;\n      display: inline-block;\n      text-align: center;\n      color: $white;\n      font-size: 2rem;\n      line-height: 40px;\n    }\n\n    &.btn-large {\n      width: 56px;\n      height: 56px;\n\n      &.halfway-fab {\n        bottom: -28px;\n      }\n\n      i {\n        line-height: 56px;\n      }\n    }\n\n    &.halfway-fab {\n      position: absolute;\n      right: 24px;\n      bottom: -20px;\n    }\n  }\n\n  .card-reveal {\n    padding: 24px;\n    position: absolute;\n    background-color: $white;\n    width: 100%;\n    overflow-y: auto;\n    left: 0;\n    top: 100%;\n    height: 100%;\n    z-index: 3;\n    transition: height 1s ease-in-out;\n    display: none;\n\n    .card-title {\n      cursor: pointer;\n    }\n  }\n\n  // any h* will signal the larger size should render as 24sp\n  .card-title {\n    letter-spacing: 0.03rem;\n    font-size: 1.25rem;\n    margin-bottom: 0.25rem;\n  }\n\n  .card-footer {\n    // assume footer is the set of actions?\n    padding: 1rem 1.5rem;\n    background: transparent;\n    overflow: hidden;\n  }\n\n  &.card-inverse {\n    .card-blockquote .blockquote-footer,\n    .card-link,\n    .card-subtitle,\n    .card-text,\n    .card-title,\n    .card-footer,\n    a,\n    a:hover,\n    label,\n    span,\n    .form-group:hover,\n    div,\n    input {\n      color: $white;\n    }\n\n    .form-group.is-focused {\n      .checkbox label,\n      label.checkbox-inline,\n      .radio label,\n      label.radio-inline {\n        color: $white;\n      }\n    }\n\n    hr {\n      border-color: $white;\n    }\n\n    .checkbox {\n      label {\n        color: $white;\n\n        .checkbox-decorator {\n          .check {\n            border-color: rgba($white, 0.54);\n          }\n        }\n      }\n    }\n\n    input {\n      &::-webkit-input-placeholder,\n      &::-moz-placeholder,\n      &:-ms-input-placeholder,\n      &:-moz-placeholder {\n        color: $white;\n      }\n    }\n  }\n}\n","// dropdown, dropup, dropright, dropleft\n.dropdown,\n.dropup,\n.dropright,\n.dropleft {\n  .dropdown-menu {\n      box-shadow: 0 4px 14px rgba($black, 0.15);\n      .dropdown-header {\n          margin-top: 0.4rem;\n          padding: $dropdown-item-padding-y $dropdown-item-padding-x;\n          text-transform: uppercase;\n          font-size: 0.9rem;\n          font-weight: 600;\n      }\n      .dropdown-item {\n          width: auto;\n          &:active {\n              background-color: $primary !important;\n          }\n      }\n      // Multi-level dropdown\n      .dropdown-submenu {\n          position:relative;\n          &:hover {\n              .dropdown-menu {\n                  display:block\n              }\n          }\n          > a{\n            &.dropdown-item{\n              border-top-left-radius: $dropdown-border-radius;\n              border-top-right-radius: $dropdown-border-radius;\n            }\n          }\n          .dropdown-menu {\n              top:0;\n              left:100%;\n              margin-top:-2px;\n              margin-left:-1px;\n              &.open-left{\n                  left: auto;\n                  right: 100%;\n              }\n          }\n          &:last-child{\n              > .dropdown-item:first-child {\n                  border-bottom-left-radius: calc(#{$dropdown-border-radius} - 1px);\n                  border-bottom-right-radius: calc(#{$dropdown-border-radius} - 1px);\n              }\n          }\n          > a:after {\n              font-family: 'FontAwesome';\n              content: \"\\f105\" !important;\n              right: 14px;\n              top: -10px;\n              font-size: 1.2rem;\n              float: right;\n              border: none;\n              margin-top: 1.15rem;\n              position: absolute;\n          }\n      }\n      .disabled {\n          color: $gray-500;\n      }\n  }\n}\n\n// Add extra class to `.dropdown-menu` to flip the alignment of the dropdown menu with the parent.\n.dropdown-menu-right {\n  left: auto !important;\n  right: 0;\n}\n\n\n// Arrow\n.dropdown-menu{\n    &.arrow {\n        margin-top: 12px;\n        &:after, &:before {\n            position: absolute;\n            left: 10px;\n            display: inline-block;\n            width: 0;\n            height: 0;\n            content: '';\n            border: 7px solid transparent;\n            border-top-width: 0;\n        }\n        &:before{\n            top: -7px;\n            border-bottom-color: $border-color;\n        }\n        &:after {\n            top: -6px;\n            border-bottom-color: $white;\n        }\n    }\n    .arrow-left{\n        margin-left: 7px !important;\n        &:after, &:before {\n            position: absolute;\n            top: 15px;\n            display: inline-block;\n            width: 0;\n            height: 0;\n            content: '';\n            border: 7px solid transparent;\n            border-left-width: 0;\n        }\n        &:before{\n            left: -8px;\n            border-right-color: $border-color;\n        }\n        &:after {\n            left: -7px;\n            border-right-color: $white;\n        }\n    }\n    .arrow-right{\n        margin-right: 7px !important;\n        &:after, &:before {\n            position: absolute;\n            top: 15px;\n            display: inline-block;\n            width: 0;\n            height: 0;\n            content: '';\n            border: 7px solid transparent;\n            border-right-width: 0;\n        }\n        &:before {\n            right: -8px;\n            border-left-color: lighten($black, 70%);\n        }\n        &:after {\n            right: -7px;\n            border-left-color: $white;\n        }\n    }\n}\n\n// dropup\n.dropup {\n    .dropdown-submenu {\n        .dropdown-menu {\n            bottom: auto;\n        }\n    }\n}\n\n.show {\n  > .dropdown-menu {\n    transform: scale(1, 1);\n    opacity: 1;\n    display: block;\n  }\n}\n\n// Horizontal layout navigation menu dropdown arrow\n.navbar-horizontal {\n  .dropdown-submenu {\n    .dropdown-toggle::after {\n      top: -9px !important;\n      margin-top: 1.25rem;\n      right: 10px;\n    }\n  }\n}\n",".carousel-inner > .carousel-item > img {\n    max-width: 100%;\n    height: auto;\n}\n","// Modals\n\n// modal content\n.modal-content {\n  overflow: hidden;\n  // modal header\n  .modal-header {\n    border-radius: 0;\n    &[class*=\"bg-\"] > * {\n      color: $white;\n    }\n  }\n  // modal footer\n  .modal-footer {\n    .btn {\n      margin: 0;\n      & + .btn {\n        margin-left: 8px;\n      }\n    }\n  }\n}\n@include media-breakpoint-down(xs) {\n  .modal-open .modal{\n    padding: 0 .5rem !important;\n  }\n}\n","// Vertical Tabs\n.nav-vertical{\n\toverflow: hidden;\n\t.nav-left{\n\t\t&.nav-tabs {\n\t\t\tfloat: left;\n\t\t\tborder-bottom: 0;\n\t\t\tborder-radius: 0;\n\t\t\tdisplay: table;\n\n\t\t\tli.nav-item {\n\t\t\t\tmargin: 0;\n\t\t\t\ta.nav-link {\n\t\t\t\t\tmin-width: 6.5rem;\n\t\t\t\t\tborder-right: 1px solid $gray-300;\n\t\t\t\t\t&.active{\n\t\t\t\t\t\tborder-right: 2px solid $primary !important;\n\t\t\t\t\t\tborder-bottom: 0;\n\t\t\t\t\t}\n\t\t\t\t}\n\t\t\t}\n\t\t}\n\t\t~.tab-content {\n\t\t\t.tab-pane {\n\t\t\t\tdisplay: none;\n\t\t\t\tpadding-left: 1rem;\n\t\t\t\toverflow-y: auto;\n\t\t\t\t&.active { display: block; }\n\t\t\t}\n\t\t}\n\t}\n\t.nav-right{\n\t\t&.nav-tabs {\n\t\t\tfloat: right;\n\t\t\tborder-bottom: 0;\n\t\t\tborder-radius: 0;\n\t\t\tdisplay: table;\n\n\t\t\tli.nav-item {\n\t\t\t\tmargin: 0;\n\t\t\t\ta.nav-link{\n\t\t\t\t\tborder-left: 1px solid $gray-300;\n\t\t\t\t\tmin-width: 6.5rem;\n\t\t\t\t\t&.active{\n\t\t\t\t\t\tborder-left: 2px solid $primary !important;\n\t\t\t\t\t\tborder-bottom: 0;\n\t\t\t\t\t}\n\t\t\t\t}\n\t\t\t}\n\t\t}\n\t\t~.tab-content {\n\t\t\t.tab-pane {\n\t\t\t\tdisplay: none;\n\t\t\t\tpadding-right: 1rem;\n\t\t\t\toverflow-y: auto;\n\t\t\t\t&.active { display: block; }\n\t\t\t}\n\t\t}\n\t}\n}\n\n// Tabs/Navs/Pills\n.tab-content{\n\tpadding: 0.375rem;\n}\n.nav-tabs{\n    .nav-link{\n\t\theight: 100%;\n\t\tborder: 0;\n\t\tborder-radius: 0;\n        &.active{\n            border-bottom: 2px solid $primary;\n        }\n    }\n    .nav-item{\n        &.show{\n            .nav-link{\n                background-color: transparent;\n                border-bottom-color: $white;\n            }\n        }\n    }\n}\n","@import \"../core/colors/palette-variables\";\n\n.vertical-layout.navbar-sticky {\n  &.page-scrolled {\n    &:not(.layout-dark) {\n      nav.header-navbar {\n        background-color: $white;\n        @include card-shadow();\n      }\n    }\n  }\n}\nnav.navbar.header-navbar {\n  padding: 0.3rem 1rem 0.3rem 1.5rem !important;\n\n  .container-fluid {\n    padding-left: 0;\n  }\n}\n.navbar {\n  z-index: 4; // required to make the shadow overlap the below content (need is visible in mobile drawer overlay)\n  border: 0;\n  border-radius: 0;\n  padding: .7rem 1rem 0.7rem 1.5rem !important;\n\n  .navbar-brand {\n    position: relative;\n  }\n\n  //center align brand option for horizontal layouts\n  .navbar-brand-center{\n    .navbar-nav{\n      padding-top: 0;\n    }\n    .navbar-header {\n      width: 120px !important;\n      position: absolute;\n      left: 50%;\n      top: 0;\n      padding: 0;\n      margin: 0;\n      transform: translate(-50%, 0);\n      .logo {\n        .logo-text{\n          text-transform: uppercase;\n          padding: 7px 0;\n          display: block;\n          font-size: 1.6rem;\n          letter-spacing: 2px;\n          text-align: left;\n          font-weight: 400;\n        }\n        .logo-img{\n          width: 30px;\n          display: inline-block;\n          max-height: 30px;\n          margin-right: 15px;\n        }\n      }\n    }\n  }\n\n  .navbar-nav {\n    display: block;\n    margin-right: -18px;\n\n    .dropdown-menu {\n      position: absolute;\n    }\n\n    // Search\n    .nav-search {\n      .nav-link-search {\n        float: left;\n      }\n      .search-input {\n        .search-input-icon {\n          display: none;\n          position: absolute;\n          left: 2rem;\n          top: 1.44rem;\n          cursor: pointer;\n        }\n        .input {\n          width: 0;\n          border: none;\n          background: none;\n          line-height: 1rem;\n        }\n        .search-input-close {\n          z-index: 101;\n          display: none;\n          position: absolute;\n          right: 2rem;\n          top: 32%;\n          cursor: pointer;\n        }\n        .search-list {\n          position: absolute;\n          top: 100%;\n          left: 0;\n          background: $white;\n          width: 100%;\n          margin-top: 1rem;\n          padding-left: 0;\n          border-radius: 0.267rem;\n          display: none;\n          z-index: 1200;\n          &.show {\n            display: block;\n            overflow: hidden;\n            width: 100%;\n          }\n          li {\n            a {\n              padding: 0.8rem 1rem;\n              color: $body-color;\n            }\n            &:first-child,\n            &:last-child {\n              border-top-left-radius: $border-radius;\n              border-top-right-radius: $border-radius;\n            }\n            &:hover,\n            &.current_item {\n              background-color: map-get($grey-color, lighten-4);\n            }\n          }\n        }\n        &:not(.open){\n          overflow: hidden;\n        }\n\n        // open\n        &.open {\n          position: absolute;\n          left: 0;\n          background: $white;\n          right: 0;\n          top: 0;\n          width: 100%;\n          z-index: 115;\n          margin-top: -1px;\n          box-shadow: -8px 12px 18px 0 rgba($black, 0.13);\n\n          .search-input-icon,\n          .search-input-close {\n            display: block;\n          }\n          .search-input-close {\n            margin-top: 0.1rem;\n          }\n          .input {\n            width: 100%;\n            padding: 1.37rem 4.3rem;\n          }\n        }\n      }\n    }\n  }\n\n  .navbar-toggle {\n    background-color: transparent;\n    border: none;\n  }\n\n\n  //-------------Navbar Container-------------\n  //---------------------------------------\n\n  // Dropdown toggle\n  .navbar-container {\n    .navbar-nav {\n      .dropdown-toggle {\n        &::after {\n          content: none;\n        }\n      }\n      .notification-dropdown {\n        border: transparent;\n        width: 25rem;\n        &.dropdown-menu{\n          padding: 0;\n        }\n        .dropdown-menu-header {\n          .dropdown-header {\n            text-transform: none;\n          }\n        }\n        .scrollable-container {\n          position: relative;\n          max-height: 20rem;\n          .ps__rail-x {\n            display: none;\n          }\n          .read-notification {\n            background-color: map-get($grey-color, \"lighten-4\");\n          }\n        }\n        .media {\n          width: 100%;\n          padding: 1rem;\n          border-bottom: 1px solid $border-color;\n        }\n        .noti-text{\n          color: $gray-600;\n          font-size: 0.8rem;\n        }\n        .noti-footer {\n          &:hover, &:focus {\n            color: theme-color(primary) !important;\n            text-decoration: none;\n          }\n        }\n      }\n      .user {\n        flex-direction: column;\n        line-height: 1.1rem;\n      }\n    }\n\n    .notification {\n      position: relative;\n      top: -15px;\n      right: 14px;\n      padding: 0.25rem 0.4rem;\n    }\n  }\n\n  // Notification dropdown\n  .notification-dropdown{\n    .dropdown-item.active, .dropdown-item:active{\n      i,.noti-title, .noti-text{\n        color: $white !important;\n      }\n    }\n  }\n\n  // flag img\n  .langimg{\n    width: 26px;\n    margin-right: 5px;\n  }\n}\n\n@include media-breakpoint-up(xl) {\n  .vertical-layout.navbar-sticky {\n    &.nav-collapsed {\n      nav.header-navbar {\n        width: calc(100% - 60px) !important;\n      }\n    }\n    &.sidebar {\n      &-sm {\n        nav.header-navbar {\n          width: calc(100% - 220px);\n        }\n      }\n      &-lg {\n        nav.header-navbar {\n          width: calc(100% - 280px);\n        }\n      }\n    }\n    nav.header-navbar {\n      width: calc(100% - 250px);\n    }\n  }\n}\n\n@media (min-width: 992px) {\n  .navbar.header-navbar{\n    .navbar-nav {\n      display: flex;\n      align-items: center;\n    }\n  }\n}\n\n@media (max-width: 991.98px) {\n  .header-navbar .navbar-container ul.navbar-nav{\n    .dropdown-language{\n      margin-top: 0.4rem;\n    }\n    .dropdown-notification{\n      margin-top: 1.2rem !important;\n    }\n  }\n  .navbar {\n    .navbar-brand-center{\n      .navbar-header {\n        width: 30px !important;\n        .logo {\n          .logo-img{ margin-right: 0;}\n          .logo-text .text{\n            display: none;\n          }\n        }\n      }\n    }\n  }\n}\n@include media-breakpoint-down(sm) {\n  .header-navbar .navbar-container ul.navbar-nav{\n    .dropdown-language{\n      margin-top: 0.8rem;\n    }\n  }\n}\n@include media-breakpoint-down(xs) {\n  nav.navbar.header-navbar {\n    padding: .3rem .5rem !important;\n  }\n}\n\n@media(max-width: 525px) {\n  .header-navbar .navbar-container .notification-dropdown {\n    right: -2rem;\n    width: 350px !important;\n  }\n}\n\n@media(max-width: 420px) {\n  .header-navbar .navbar-container .notification-dropdown {\n    right: -3rem;\n    width: calc(100vw - 1.7rem) !important;\n  }\n}\n","// Overrides user components-variables\n@import \"~bootstrap/scss/functions\";\n\n@import \"../variables/variables\";\n@import \"~bootstrap/scss/variables\";\n$white-color: (\n  \"base\": #ffffff\n);\n$black-color: (\n  \"base\": #000000\n);\n$primary-color: (\n  \"lighten-4\": #EFDEFF,\n  \"lighten-3\": #DCBDFF,\n  \"lighten-2\": #C89CFF,\n  \"lighten-1\": #B583FF,\n  \"base\": #975AFF,\n  \"darken-1\": #7441DB,\n  \"darken-2\": #562DB7,\n  \"darken-3\": #3B1C93,\n  \"darken-4\": #28117A\n);\n$secondary-color: (\n  \"lighten-4\": #EAF0F9,\n  \"lighten-3\": #D6E1F3,\n  \"lighten-2\": #B5C3DB,\n  \"lighten-1\": #8F9DB8,\n  \"base\": #616D89,\n  \"darken-1\": #465375,\n  \"darken-2\": #303C62,\n  \"darken-3\": #1E294F,\n  \"darken-4\": #121A41\n);\n$success-color: (\n  \"lighten-4\": #E0FBDA,\n  \"lighten-3\": #BDF8B6,\n  \"lighten-2\": #8EEC8D,\n  \"lighten-1\": #6CD975,\n  \"base\": #40C057,\n  \"darken-1\": #2EA54F,\n  \"darken-2\": #208A47,\n  \"darken-3\": #146F3E,\n  \"darken-4\": #0C5C38\n);\n$danger-color: (\n  \"lighten-4\": #FEE8DC,\n  \"lighten-3\": #FECBBA,\n  \"lighten-2\": #FCA897,\n  \"lighten-1\": #F9877C,\n  \"base\": #F55252,\n  \"darken-1\": #D23B48,\n  \"darken-2\": #B02940,\n  \"darken-3\": #8E1A38,\n  \"darken-4\": #750F33\n);\n$warning-color: (\n  \"lighten-4\": #FEEFD0,\n  \"lighten-3\": #FEDBA1,\n  \"lighten-2\": #FCC173,\n  \"lighten-1\": #FAA750,\n  \"base\": #F77E17,\n  \"darken-1\": #D46010,\n  \"darken-2\": #B1460B,\n  \"darken-3\": #8F2F07,\n  \"darken-4\": #762004\n);\n$info-color: (\n  \"lighten-4\": #D5F1FD,\n  \"lighten-3\": #ACE0FC,\n  \"lighten-2\": #81C8F7,\n  \"lighten-1\": #60AFF0,\n  \"base\": #2F8BE6,\n  \"darken-1\": #226CC5,\n  \"darken-2\": #1750A5,\n  \"darken-3\": #0E3885,\n  \"darken-4\": #09276E\n);\n$light: (\n  \"base\": #DBDEE5\n);\n$dark: (\n  \"base\": #1A051D\n);\n\n$grey-color: (\n  \"lighten-4\": #f5f5f5,\n  \"lighten-3\": #eee,\n  \"lighten-2\": #e0e0e0,\n  \"lighten-1\": #bdbdbd,\n  \"base\": #9e9e9e,\n  \"darken-1\": #757575,\n  \"darken-2\": #616161,\n  \"darken-3\": #424242,\n  \"darken-4\": #212121\n);\n\n$shades: (\n  \"black\": #000000,\n  \"white\": #ffffff,\n  \"transparent\": transparent\n);\n\n$colors: (\n  \"white\": $white-color,\n  \"black\": $black-color,\n  \"primary\": $primary-color,\n  \"secondary\": $secondary-color,\n  \"success\": $success-color,\n  \"danger\": $danger-color,\n  \"warning\": $warning-color,\n  \"info\": $info-color,\n  \"light\": $light,\n  \"dark\": $dark,\n  \"grey\": $grey-color\n);\n\n$gradient-colors: (\n  gradient-mint: (\n    \"start-color\": #226CC5,\n    \"end-color\": #6CD975\n  ),\n  gradient-pomegranate: (\n    \"start-color\": #465375,\n    \"end-color\": #F55252\n  ),\n  gradient-blackberry: (\n    \"start-color\": #303C62,\n    \"end-color\": #B5C3DB\n  ),\n  gradient-king-yna: (\n    \"start-color\": #7441DB,\n    \"end-color\": #F9877C\n  ),\n  gradient-ibiza-sunset: (\n    \"start-color\": #B02940,\n    \"end-color\": #FCC173\n  ),\n  gradient-flickr: (\n    \"start-color\": #F77E17,\n    \"end-color\": #2F8BE6\n  ),\n  gradient-purple-bliss: (\n    \"start-color\": #562DB7,\n    \"end-color\": #60AFF0\n  ),\n  gradient-man-of-steel: (\n    \"start-color\": #2F3EA0,\n    \"end-color\": #AE342D\n  ),\n  gradient-purple-love: (\n    \"start-color\": #7441DB,\n    \"end-color\": #C89CFF\n  ),\n  gradient-starfall: (\n    \"start-color\": #D23B48,\n    \"end-color\": #2F8BE6\n  )\n);\n",".popover{\n    @include card-shadow();\n    .popover-header {\n        @include border-top-radius(0.35rem);\n    }\n    &.bs-popover-bottom {\n        > .arrow::after {\n            border-bottom-color: $body-bg;\n        }\n    }\n}","// Progress Bar\n.progress {\n    .progress-bar {\n        border-radius: 1.28rem;\n    }\n}","// Toasts\n\n// Toast container\n.toast-bs-container{\n  z-index: $zindex-popover;\n  position: fixed;\n  top: 4rem;\n  right: 2rem;\n  width: calc(100% - 4rem);\n  // Toast position\n  .toast-position{\n    display: flex;\n    flex-direction: column;\n    flex-wrap: wrap-reverse;\n    // Toast shown\n    .toast.show{\n      z-index: $zindex-popover;\n    }\n  }\n}\n\n// Toast translucent\n.toast {\n  display: none;\n  &.toast-translucent {\n    opacity: 1;\n    display: block;\n  }\n}\n// Toast placement\n.toast-placement{\n  .toast{\n    position: absolute;\n    top: 2rem;\n    z-index: $zindex-popover;\n  }\n}\n// toast body\n.toast-body {\n  color: $body-color;\n}\n","// This needs review, including colors: https://www.google.com/design/spec/style/typography.html#typography-styles\n@import \"../core/colors/palette-variables\";\n\nblockquote, .blockquote{\n  footer, small, .small{\n    font-size: 80%;\n    padding-left: 0;\n  }\n}\n\n.blockquote-reverse, blockquote.pull-right{\n  padding-right: 15px;\n  padding-left: 0;\n  border-right: 5px solid map-get($grey-color, lighten-3);\n  border-left: 0;\n  text-align: right;\n}\n\nh2.title{\n  margin-bottom: $spacer * 2;\n}\n\n.description,\n.card-description,\n.footer-big p{\n  color: color(\"gray\");\n}\n\n.text-warning {\n  color: theme-color(\"warning\") !important;\n}\n.text-primary {\n  color: theme-color(\"primary\") !important;\n}\n.text-danger {\n  color: theme-color(\"danger\") !important;\n}\n.text-success {\n  color: theme-color(\"success\") !important;\n}\n.text-info {\n  color: theme-color(\"info\") !important;\n}\n\n//small\n.font-small-1{\n  font-size: 0.7rem !important;\n}\n.font-small-2{\n  font-size: 0.8rem !important;\n}\n.font-small-3{\n  font-size: 0.9rem !important;\n}\n//medium\n.font-medium-1{\n  font-size: 1.1rem !important;\n}\n.font-medium-2{\n  font-size: 1.2rem !important;\n}\n.font-medium-3{\n  font-size: 1.3rem !important;\n}\n.font-medium-4{\n  font-size: 1.4rem !important;\n}\n.font-medium-5{\n  font-size: 1.5rem !important;\n}\n//large\n.font-large-1{\n  font-size: 2rem !important;\n  line-height: 2rem;\n}\n.font-large-2{\n  font-size: 3rem !important;\n  line-height: 2.95rem;\n}\n.font-large-3{\n  font-size: 4rem !important;\n  line-height: 3.5rem;\n}\n.font-large-4{\n  font-size: 5rem !important;\n  line-height: 4.2rem;\n}\n.font-large-5{\n  font-size: 6rem !important;\n  line-height: 5.2rem;\n}\n\n// Text styling\n// -------------------------\n\n// Font weights\n\n.text-bold-300 {\n  @include font-weight(light);\n}\n.text-bold-400 {\n  @include font-weight(book);\n}\n.text-bold-500 {\n  @include font-weight(medium);\n}\n.text-bold-600 {\n  @include font-weight(semi-bold);\n}\n.text-bold-700 {\n  @include font-weight(bold);\n}\n\n// Font style\n.text-italic {\n    font-style: italic;\n}\n.text-highlight {\n    padding: 4px 6px;\n}\n","@mixin font-weight($weight) {\n  $weights: (\n    thin: 100,\n    extra-light: 200,\n    ultra-light: 200,\n    light: 300,\n    normal: 400,\n    book: 400,\n    regular: 400,\n    medium: 500,\n    semi-bold: 600,\n    demi-bold: 600,\n    bold: 700,\n    extra-bold: 800,\n    ultra-bold: 900,\n    heavy: 900,\n    black: 900,\n    ultra: 900,\n    ultra-black: 900,\n    extra-ultra: 900\n  );\n  \n  $output: $weight;\n  @if map-has-key($weights, $weight) {\n    $output: map-get($weights, $weight);\n  }\n  \n  font-weight: $output;\n}","// Bootstrap Grids\n\n#equal-width,\n#setting-one-column-width,\n#variable-width-content,\n#equal-width-multi-row,\n#all-breakpoints,\n#stacked-to-horizontal,\n#alignment,\n#vertical-alignment,\n#horizontal-alignment,\n#column-wrapping,\n#column-breaks,\n#reordering,\n#offsetting-columns,\n#margin-utilities,\n#nesting {\n  .bd-example {\n    .row {\n\n      [class^=\"col-\"],\n      .col {\n        padding-top: .75rem;\n        padding-bottom: .75rem;\n        background-color: rgba(86, 61, 124, .15);\n        border: 1px solid rgba(86, 61, 124, .2);\n      }\n    }\n\n    .row+.row {\n      margin-top: 1rem;\n    }\n  }\n}\n","@import \"../core/colors/palette-variables\";\n\n// inline code tag\ncode {\n    padding: 0.1rem 0.4rem;\n    background-color: map-get($grey-color, lighten-3);\n    border-radius: 0.2rem;\n}\n\npre {\n    code {\n        padding: 0;\n        text-shadow: none !important;\n        .token.operator,\n        .token.url {\n            background: transparent;\n        }\n    }\n    &[class*=\"language-\"] {\n        border-radius: $border-radius;\n    }\n}\n","// List Groups\n\n@mixin list-group-item-custom-variant($state, $background, $color) {\n  .list-group-item-#{$state} {\n    color: $color !important;\n    background-color: $background !important;\n\n    &.list-group-item-action {\n      @include hover-focus {\n        color: $color !important;\n        background-color: darken($background, 5%) !important;\n      }\n    }\n  }\n}\n","// Contextual variants\n//\n// Add modifier classes to change text and background color on individual items.\n// Organizationally, this must come after the `:hover` states.\n\n@each $color, $value in $theme-colors {\n    @include list-group-item-custom-variant($color, theme-color-level($color, -9), theme-color-level($color, 6));\n}\n\n.list-group{\n  i{\n    display: inline-block;\n  }\n}\n\n// Inline users list\n.users-list{\n  li {\n    &+li {\n      margin-left: -0.65rem;\n    }\n    img{\n      border: 2px solid $white;\n    }\n  }\n}\n","@import \"../core/colors/palette-variables\";\n\nform {\n  label {\n    letter-spacing: 0.5px;\n  }\n\n  .form-section {\n    color: $headings-color;\n    line-height: 3rem;\n    font-size: 1.2rem;\n    letter-spacing: 0.5px;\n    font-weight: 400;\n    margin-bottom: 20px;\n    border-bottom: 1px solid $border-color;\n    i {\n      font-size: 20px;\n      margin-left: 5px;\n      margin-right: 5px;\n    }\n  }\n\n  .form-body {\n    overflow: hidden;\n  }\n\n  .form-actions {\n    border-top: 1px solid darken($body-bg, 10%);\n    padding: 20px 0;\n    margin-top: 20px;\n    &.filled {\n      background-color: $body-bg;\n    }\n    &.center {\n      text-align: center;\n    }\n    &.right {\n      text-align: right;\n    }\n    &.top {\n      border-top: 0;\n      border-bottom: 1px solid darken($body-bg, 10%);\n      margin-top: 0;\n      margin-bottom: 20px;\n    }\n    @include media-breakpoint-down(sm) {\n      .buttons-group {\n        float: left !important;\n        margin-bottom: 10px;\n      }\n    }\n  }\n\n  &.form-bordered {\n    .form-group {\n      margin: 0;\n      border-bottom: 1px solid $border-color;\n      .label-control {\n        padding-top: 1.5rem;\n      }\n      > div {\n        padding: 1.2rem;\n        border-left: 0;\n        @include media-breakpoint-up(md) {\n          border-left: 1px solid $border-color;\n        }\n      }\n      &.last {\n        border-bottom: 0;\n      }\n    }\n  }\n\n  &.striped-rows {\n    .form-group {\n      margin: 0;\n      .label-control {\n        padding-top: 1.5rem;\n      }\n      > div {\n        padding: 1.2rem;\n      }\n      &:nth-child(even) {\n        background-color: $body-bg;\n      }\n    }\n  }\n}\n\n// label\nform,\n.form-group {\n  label {\n    text-transform: uppercase;\n    font-size: 0.85rem;\n    font-weight: 500;\n  }\n}\n\n//----------form-control------------\n.form-control {\n  background-clip: inherit;\n\n  &:focus{\n    // placeholder transition on focus-in\n    &::placeholder{\n      transform: translate(5px);\n      transition: all .3s ease;\n    }\n    ~.form-control-position{\n      i{\n        color: map-get($primary-color, base);\n      }\n    }\n  }\n  // placeholder transition on focus-out\n  &:not(:focus){\n    &::placeholder{\n      transition: all .3s ease;\n    }\n  }\n  // placeholer\n  &::-webkit-input-placeholder {\n    color: $text-muted;\n  }\n  /* Firefox 18- */\n  &:-moz-placeholder {\n    color: $text-muted;\n  }\n  /* Firefox 19+ */\n  &::-moz-placeholder {\n    color: $text-muted;\n  }\n  &:-ms-input-placeholder {\n    color: $text-muted;\n  }\n\n  // validation feedback\n  &[class*=\"is-\"] {\n    ~[class*=\"-feedback\"] {\n      i:before {\n        font-size: 0.5rem;\n        margin-right: 2px;\n        position: relative;\n        top: -1px;\n      }\n    }\n  }\n}\n\n//---------Icons inside input-----------\n// form-control-position\n.form-control-position {\n  position: absolute;\n  top: 0;\n  z-index: 2;\n  display: block;\n  width: 2.5rem;\n  line-height: 2.5rem;\n  text-align: center;\n}\n// icon on left\n.has-icon-left {\n  .form-control-position {\n    right: auto;\n  }\n  .form-control {\n    padding-left: 2.8rem;\n  }\n}\n// icon on right\n.has-icon-right {\n  .form-control {\n    padding-right: 2.8rem;\n  }\n  .form-control-position {\n    right: 0;\n  }\n}\n\n\n//---------validation tooltip-----------\n.needs-validation {\n  .valid-tooltip,\n  .invalid-tooltip {\n    margin-top: 0.2rem;\n  }\n}\n\n// input group\n.input-group {\n  &-prepend,\n  &-append {\n    .checkbox {\n      label {\n        &:before,\n        &:after {\n          top: 4px !important;\n          left: 5px !important;\n        }\n      }\n      input:checked {\n        &~label:after {\n          top: 8px !important;\n          left: 8px !important;\n        }\n      }\n    }\n    .radio {\n      label::before {\n        margin-right: 0;\n      }\n    }\n  }\n  button {\n    line-height: 1;\n  }\n}\n\n// Custom checkbox\n.custom-control {\n  &.inline {\n    display: inline;\n  }\n}\n.custom-checkbox {\n  input.custom-control-input ~ .custom-control-label {\n    &:before {\n      width: 1.1rem;\n      height: 1.1rem;\n      top: 0.125rem;\n      left: -1.6rem;\n      border-radius: 0.3rem;\n    }\n    &:after {\n      top: 0.15rem;\n      left: -1.55rem;\n    }\n  }\n}\n\n.form-control {\n  &:disabled,\n  &[readonly] {\n    border-color: map-get($grey-color, lighten-2);\n  }\n}\n\n.custom-select {\n  -moz-appearance: none;\n  -webkit-appearance: none;\n}\n\n\n//-------------Radio----------------\n//----------------------------------\n.radio{\n  input[type=\"radio\"] {\n    /* Basic Bootstrap Radio Buttons Hide */\n    display: none;\n\n    // Checked Radio\n    &:checked{\n      ~label::after {\n        opacity: 1;\n        transform : scale(1);\n        transition : all .3s cubic-bezier(.35,.9,.4,.9);\n      }\n    }\n    // Disabled Radio\n    &:disabled{\n      &~label {\n        color: $gray-500;\n      }\n      ~label::before {\n        background-color: $body-bg !important;\n        border: 1px solid $input-border-color !important;\n      }\n      ~label::after {\n        background-color: map-get($map: $grey-color, $key: lighten-1);\n      }\n    }\n  }\n  // Radio Buttons Label\n  label {\n    position: relative;\n    display: flex;\n    align-items: center;\n    margin-bottom: 0;\n\n    // Before - Outer Circle\n    &::before {\n      content: \"\";\n      border: 1px solid map-get($primary-color, base);\n      border-radius: 50%;\n      width: 20px;\n      height: 20px;\n      margin-right: 0.5rem;\n    }\n    // After - Inner Circle\n    &::after {\n      position: absolute;\n      content: \"\";\n      height: 12px;\n      width: 12px;\n      background-color: map-get($primary-color, base);\n      border-radius: 50%;\n      left: 4px;\n      opacity: 0;\n      transform : scale(3.6);\n    }\n  }\n  // small radio\n  &.radio-sm {\n    input {\n      &~label::before{\n        width: 15px;\n        height: 15px;\n      }\n      &~label::after{\n        width: 9px;\n        height: 9px;\n        left: 3px;\n      }\n    }\n  }\n}\n\n\n//-------------Checkbox-------------\n//----------------------------------\n.checkbox{\n  position: relative;\n\n  input[type='checkbox'] {\n    // basic checkbox - display none\n    display: none;\n    // checkbox checked\n    &:checked {\n      &~label {\n        // after\n        &:after {\n          border-top-style: none;\n          border-right-style: none;\n          -ms-transform: rotate(-45deg);    // IE9\n          transform: rotate(-45deg);\n          height: 0.4rem;\n          width: 0.8rem;\n          border-color: map-get($primary-color, base);\n          border-radius: 0;\n          border-width: 2px;\n          top: 6px;\n          left: 4px;\n        }\n      }\n    }\n    // disabled checkbox\n    &:disabled{\n      ~label{\n        color: $gray-500;\n        // before\n        &:before {\n          background-color: $body-bg !important;\n          box-shadow: none !important;\n        }\n      }\n      &:checked {\n        ~label {\n          // after\n          &:after {\n            border-color: map-get($grey-color, lighten-1);\n          }\n        }\n      }\n    }\n  }\n\n  label{\n    // label css in check box\n    margin-left: 1.75rem;\n    margin-bottom: 0;\n\n    // Before - Outer\n    &:before {\n      content: '';\n      height: 20px;\n      width: 20px;\n      position: absolute;\n      top: 0;\n      left: 0;\n      border-radius: $border-radius;\n      border: 1px solid $input-border-color;\n    }\n    // After - Inner\n    &:after {\n      content: ' ';\n      height: 20px;\n      width: 20px;\n      border: 1px solid $input-border-color;\n      position: absolute;\n      border-radius: $border-radius;\n      top: 0px;\n      left: 0;\n      transition: 100ms ease-in-out;\n    }\n  }\n\n  // Small Checkbox\n  &.checkbox-sm{\n    // checked checkbox\n    input:checked ~ label:after{\n      height: 5px;\n      width: 8px;\n      top: 7px;\n      left: 6px;\n    }\n    label {\n      // unchecked checkbox\n      &::before, &::after {\n        width: 15px;\n        height: 15px;\n        top: 3px;\n        left: 3px;\n      }\n    }\n  }\n}\n\n//-------------Switch---------------\n//----------------------------------\n.custom-switch {\n  input[type='checkbox'] {\n    &:focus:not(:checked) ~ .custom-control-label::before {\n      border-color: $gray-500;\n    }\n    &~label {\n      // Before - Outer bg, border\n      &:before {\n        top: 0;\n        height: 1.5rem;\n        border-radius: 1rem;\n        background-color: transparent;\n      }\n      // After - Inner circle\n      &:after {\n        top: 2px;\n        left: -2.9rem;\n        width: 1.2rem;\n        height: 1.2rem;\n        border-radius: 1rem;\n        transition: all .25s ease;\n      }\n    }\n    // checked switch\n    &:checked {\n      &~label {\n        &:after {\n          left: -3.5rem;\n          background-color: $white !important;\n        }\n      }\n    }\n  }\n}\n\n\n//--------Input tag---------\n.form-control.tagging {\n  height: auto;\n  .tag {\n    padding: 4px 28px 6px 0;\n    margin: 5px 10px 5px 0;\n    background: map-get($primary-color, lighten-4);\n    color: map-get($primary-color, base);\n    border-radius: $border-radius;\n    span {\n      padding: 5px 12px 8px;\n      background: map-get($primary-color, base);\n      color: map-get($primary-color, lighten-4);\n      border-bottom-left-radius: $border-radius;\n      border-top-left-radius: $border-radius;\n    }\n    .tag-i {\n      font-size: 1.5rem;\n      top: 0.95rem;\n      right: 0.65rem;\n      color: map-get($primary-color, base);\n      text-decoration: none;\n    }\n  }\n  input.type-zone {\n    min-width: 0;\n    background-color: transparent;\n  }\n}\n\n\n//--------Editor---------\n.ql-editor {\n  pre.ql-syntax {\n    border-radius: $border-radius;\n  }\n}\n","// ================================================================================================\n//   File Name: pallette.scss\n//   Description: Custom color system styles, includes background, border and text colors\n// ================================================================================================\n\n//  ================================================================================================\n//  WARNING: PLEASE DO NOT CHANGE THIS VARIABLE FILE.\n//  THIS FILE WILL GET OVERWRITTEN WITH EACH APEX TEMPLATE RELEASE.\n//  TIP:\n//  We suggest you to use this (assets/scss/colors/palette.scss) file for overriding color variables.\n//  ================================================================================================\n\n// usage: color(\"name_of_color\", \"type_of_color\")\n// to avoid to repeating map-get($colors, ...)\n\n@function color-function($color, $type) {\n  @if map-has-key($colors, $color) {\n    $curr_color: map-get($colors, $color);\n    @if map-has-key($curr_color, $type) {\n      @return map-get($curr_color, $type);\n    }\n  }\n  @return null;\n}\n\n// Color palettes\n@import \"palette-variables\";\n\n@each $gcolor_name, $gcolor in $gradient-colors {\n  $start-color: map-get($gcolor, start-color);\n  $end-color: map-get($gcolor, end-color);\n  .#{$gcolor_name} {\n    @include gradient-directional($start-color, $end-color);\n  }\n  .btn.#{$gcolor_name} {\n    border: none;\n    color: $white;\n    &:active,\n    &.active {\n      @include gradient-directional($start-color, $end-color);\n    }\n  }\n}\n\n@each $color_name, $color in $colors {\n  @each $color_type, $color_value in $color {\n    @if $color_type == \"base\" {\n      .#{$color_name} {\n        color: $color_value !important;\n      }\n      .bg-#{$color_name} {\n        background-color: $color_value !important;\n        .card-header,\n        .card-footer {\n          background-color: transparent;\n        }\n      }\n      .toast-#{$color_name} {\n        background-color: $color_value;\n      }\n      .alert-#{$color_name} {\n        border-color: $color_value !important;\n        background-color: $color_value !important;\n        color: $white !important;\n        .alert-link {\n          color: darken($color_value, 43%);\n        }\n      }\n      .border-#{$color_name} {\n        border-color: $color_value !important;\n      }\n      .border-top-#{$color_name} {\n        border-top-color: $color_value !important;\n      }\n      .border-bottom-#{$color_name} {\n        border-bottom-color: $color_value !important;\n      }\n      .border-left-#{$color_name} {\n        border-left-color: $color_value !important;\n      }\n      .border-right-#{$color_name} {\n        border-right-color: $color_value !important;\n      }\n\n      .badge-#{$color_name} {\n        background-color: $color_value;\n      }\n      .panel-#{$color_name} {\n        border-color: $color_value;\n        .panel-heading {\n          color: $white;\n          border-color: $color_value;\n          background-color: lighten($color_value, 5%);\n        }\n      }\n      .bg-#{$color_name},\n      .border-#{$color_name} {\n        &.tag-glow {\n          box-shadow: 0px 0px 10px $color_value;\n        }\n      }\n      .overlay-#{$color_name} {\n        background: rgba($color_value, 0.4);\n      }\n      .card.card-outline-#{$color_name} {\n        border-width: 1px;\n        border-style: solid;\n        border-color: $color_value;\n        background-color: transparent;\n        .card-header,\n        .card-footer {\n          background-color: transparent;\n        }\n      }\n      // to lighten bg-color\n      @if $color_name != \"white\" and $color_name != \"black\" and $color_name != \"light\" and $color_name != \"dark\" {\n        .bg-light-#{$color_name} {\n          background-color: color-function($color_name, \"lighten-4\") !important;\n          color: $color_value !important;\n          border-color: color-function($color_name, \"lighten-4\");\n        }\n      }\n\n      // buttons\n      .btn {\n        &.bg-light-#{$color_name} {\n          &:hover {\n            background-color: $color_value !important;\n            color: $white !important;\n            border-color: $color_value;\n          }\n        }\n      }\n      .btn-#{$color_name} {\n        &:active {\n          background-color: darken($color_value, 1%);\n          border-color: darken($color_value, 1%);\n        }\n        &:hover {\n          background-color: lighten($color_value, 3%);\n          border-color: lighten($color_value, 3%);\n        }\n        &:focus {\n          color: $white;\n        }\n        &~.dropdown-menu {\n          .dropdown-item.active {\n            color: $white !important;\n            background-color: $color_value !important;\n          }\n        }\n      }\n      // Outline buttons\n      .btn-outline-#{$color_name} {\n        border: 1px solid;\n        border-color: $color_value;\n        background-color: transparent;\n        color: $color_value;\n        &:focus {\n          background-color: transparent !important;\n          color: $color_value !important;\n        }\n        &.active {\n          background-color: $color_value !important;\n          color: $white !important;\n        }\n        &:hover {\n          background-color: darken($color_value, 3%) !important;\n          color: $white !important;\n        }\n      }\n      // a tag buttons\n      a.btn {\n        &.btn-#{$color_name},\n        &.bg-light-#{$color_name} {\n          color: $white;\n          &:hover,\n          &:focus {\n            color: $white;\n          }\n        }\n      }\n\n      // Colored Radio\n      .radio {\n        &.radio-#{$color_name} {\n          input[type=\"radio\"] {\n            &~label::before {\n              border-color: $color_value;\n            }\n            &:checked~label::after {\n              background-color: $color_value;\n            }\n          }\n        }\n      }\n      // Colored Checkbox\n      .checkbox {\n        &.checkbox-#{$color_name} {\n          input[type=\"checkbox\"] {\n            &:checked ~ label:after {\n              border-color: $color_value;\n            }\n          }\n        }\n      }\n      // Colored Switch\n      .custom-switch {\n        &.custom-switch-#{$color_name} {\n          input[type=\"checkbox\"] {\n            &:checked ~ label:before {\n              border-color: $color_value;\n              background-color: $color_value;\n            }\n          }\n        }\n      }\n\n      // noUi slider\n      .noUi-#{$color_name} {\n        & .noUi-connect,\n        &.noUi-connect {\n          background: $color_value;\n        }\n        .noUi-handle {\n          border-color: $color_value !important;\n        }\n        &.circle-filled,\n        &.square {\n          .noUi-handle {\n            background: $color_value !important;\n          }\n        }\n      }\n\n      // switchery\n      input[type=\"checkbox\"]:checked.switchery-#{$color_name} + span {\n        border-color: $color_value !important;\n        box-shadow: $color_value 0px 0px 0px 12.5px inset !important;\n      }\n      input[type=\"checkbox\"][data-size=\"lg\"]:checked.switchery-#{$color_name} + span {\n        box-shadow: $color_value 0px 0px 0px 16px inset !important;\n      }\n      input[type=\"checkbox\"][data-size=\"sm\"]:checked.switchery-#{$color_name} + span {\n        box-shadow: $color_value 0px 0px 0px 10px inset !important;\n      }\n      input[type=\"checkbox\"][data-size=\"xs\"]:checked.switchery-#{$color_name} + span {\n        box-shadow: $color_value 0px 0px 0px 7px inset !important;\n      }\n\n      input:focus ~ .bg-#{$color_name} {\n        box-shadow: 0 0 0 0.075rem $white, 0 0 0 0.21rem $color_value !important;\n      }\n    } @else {\n      .#{$color_name}.#{$color_type} {\n        color: $color_value !important;\n      }\n      .bg-#{$color_name}.bg-#{$color_type} {\n        background-color: $color_value !important;\n      }\n      .btn-#{$color_name}.btn-#{$color_type} {\n        border-color: color-function($color_name, \"darken-2\") !important;\n        background-color: $color_value !important;\n        &:hover {\n          border-color: color-function($color_name, \"darken-2\") !important;\n          background-color: color-function($color_name, \"darken-3\") !important;\n        }\n        &:focus,\n        &:active {\n          border-color: color-function($color_name, \"darken-3\") !important;\n          background-color: color-function($color_name, \"darken-4\") !important;\n        }\n      }\n      .btn-outline-#{$color_name}.btn-outline-#{$color_type} {\n        border-color: $color_value !important;\n        color: $color_value !important;\n        &:hover {\n          background-color: $color_value !important;\n        }\n      }\n      .progress-#{$color_name}.progress-#{$color_type} {\n        background-color: $color_value;\n        &[value]::-webkit-progress-value {\n          background-color: $color_value;\n        }\n      }\n      input:focus ~ .bg-#{$color_name} {\n        box-shadow: 0 0 0 0.075rem $white, 0 0 0 0.21rem $color_value !important;\n      }\n      .border-#{$color_name}.border-#{$color_type} {\n        border: 1px solid $color_value !important;\n      }\n      .border-top-#{$color_name}.border-top-#{$color_type} {\n        border-top: 1px solid $color_value !important;\n      }\n      .border-bottom-#{$color_name}.border-bottom-#{$color_type} {\n        border-bottom: 1px solid $color_value !important;\n      }\n      .border-left-#{$color_name}.border-left-#{$color_type} {\n        border-left: 1px solid $color_value !important;\n      }\n      .border-right-#{$color_name}.border-right-#{$color_type} {\n        border-right: 1px solid $color_value !important;\n      }\n      .overlay-#{$color_name}.overlay-#{$color_type} {\n        background: $color_value; /* The Fallback */\n        background: rgba($color_value, 0.8);\n      }\n    }\n  }\n}\n","// content overlay\n.wrapper {\n  &.show-overlay {\n    .content-overlay {\n      z-index: 16;\n      // z-index: 3;\n      opacity: 1;\n    }\n  }\n  .content-overlay {\n    position: absolute;\n    opacity: 0;\n    width: 100%;\n    height: calc(100% + 54px);\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    background-color: rgba($black, 0.6);\n    cursor: pointer;\n    transition: all 0.7s;\n    z-index: -1;\n    visibility: visible;\n  }\n}\n","// Width and height\n\n.w-100 { width: 100% !important; }\n.h-100 { height: 100% !important; }\n\n// Margin and Padding\n\n.mx-auto {\n  margin-right: auto !important;\n  margin-left:  auto !important;\n}\n\na{\n  cursor: pointer;\n}\n\n// Icons\n.fonticon-wrap {\n    width: 2%;\n    float: left;\n    margin: 0.25rem;\n    i.fa{\n      font-size: 1.35rem !important;\n    }\n}\n\n.round{\n  border-radius: 1.5rem;\n}\n.square{\n  border-radius: 0;\n}\nimg{\n  &.img-border{\n    border: 5px solid $white;\n  }\n}\nul{\n  &.no-list-style{\n    list-style: none;\n  }\n}\n\n// Shadow classes\n.shadow-z-1{\n  @include shadow-z-1();\n}\n.shadow-z-1-hover{\n  @include shadow-z-1-hover();\n}\n.shadow-z-2{\n  @include shadow-z-2();\n}\n.shadow-z-3{\n  @include shadow-z-3();\n}\n.shadow-z-4{\n  @include shadow-z-4();\n}\n.shadow-z-5{\n  @include shadow-z-5();\n}\n.big-shadow{\n  @include big-shadow();\n}\n.shadow-big{\n  @include shadow-big();\n}\n.shadow-big-navbar{\n  @include shadow-big-navbar();\n}\n.sidebar-shadow{\n  @include sidebar-shadow();\n}\n.card-shadow{\n  @include card-shadow();\n}\n\n// Cursors\n\n.cursor-pointer {\n  cursor: pointer;\n}\n.cursor-move {\n  cursor: move;\n}\n.cursor-default {\n  cursor: default;\n}\n.cursor-progress {\n  cursor: progress;\n}\n.cursor-not-allowed {\n  cursor: not-allowed;\n}\n.cursor-grab {\n  cursor: grab;\n}","// ================================================================================================\n// \tFile Name: helper.scss\n// \tDescription: Helper classes provides color, width, position & background etc..\n// \t\t\t\t related customiztion.\n// ================================================================================================\n\n// Content helpers\n// -------------------------\n\n\n// Overflow\n\n.overflow-x-scroll {\n  overflow-x: scroll;\n}\n\n.overflow-y-scroll {\n  overflow-y: scroll;\n}\n\n// Lists\n.bullets-inside {\n  list-style: inside;\n}\n\n.list-style-circle {\n  list-style: circle;\n}\n\n.list-style-square {\n  list-style: square;\n}\n\n.list-style-icons {\n  padding-left: 10px;\n  margin-left: 0;\n  list-style: none;\n\n  >li i {\n    float: left;\n    width: 1em;\n    margin: 5px 12px 0 0;\n  }\n}\n\n// Borders\n// -------------------------\n\n// Border widths\n\n// Med - 2px\n.border-2 {\n  border: 2px solid;\n}\n\n.border-top-2 {\n  border-top: 2px solid;\n}\n\n.border-bottom-2 {\n  border-bottom: 2px solid;\n}\n\n.border-left-2 {\n  border-left: 2px solid;\n}\n\n.border-right-2 {\n  border-right: 2px solid;\n}\n\n// Large - 3px\n.border-3 {\n  border: 3px solid;\n}\n\n.border-top-3 {\n  border-top: 3px solid;\n}\n\n.border-bottom-3 {\n  border-bottom: 3px solid;\n}\n\n.border-left-3 {\n  border-left: 3px solid;\n}\n\n.border-right-3 {\n  border-right: 3px solid;\n}\n\n\n// No border radius\n.no-border-top-radius {\n  border-top-left-radius: 0 !important;\n  border-top-right-radius: 0 !important;\n}\n\n.no-border-bottom-radius {\n  border-bottom-left-radius: 0 !important;\n  border-bottom-right-radius: 0 !important;\n}\n\n.no-border-top-left-radius {\n  border-top-left-radius: 0 !important;\n}\n\n.no-border-top-right-radius {\n  border-top-right-radius: 0 !important;\n}\n\n.no-border-bottom-left-radius {\n  border-bottom-left-radius: 0 !important;\n}\n\n.no-border-bottom-right-radius {\n  border-bottom-right-radius: 0 !important;\n}\n\n\n// Box Shadow\n\n.box-shadow-0 {\n  box-shadow: none !important;\n}\n\n.box-shadow-1 {\n  box-shadow: 0 2px 5px 0 rgba($black, 0.16), 0 2px 10px 0 rgba($black, 0.12);\n}\n\n.box-shadow-2 {\n  box-shadow: 0 8px 17px 0 rgba($black, 0.2), 0 6px 20px 0 rgba($black, 0.19);\n}\n\n.box-shadow-3 {\n  box-shadow: 0 12px 15px 0 rgba($black, 0.24), 0 17px 50px 0 rgba($black, 0.19);\n}\n\n.box-shadow-4 {\n  box-shadow: 0 16px 28px 0 rgba($black, 0.22), 0 25px 55px 0 rgba($black, 0.21);\n}\n\n.box-shadow-5 {\n  box-shadow: 0 27px 24px 0 rgba($black, 0.2), 0 40px 77px 0 rgba($black, 0.22);\n}\n\n// Sizing\n// -------------------------\n\n//\n// Width && Height\n//\n\n.fit {\n  max-width: 100% !important;\n}\n\n.half-width {\n  width: 50% !important;\n}\n\n.full-width {\n  width: 100% !important;\n}\n\n.full-height {\n  height: 100% !important;\n}\n\n.full-height-vh {\n  height: 100vh !important;\n  height: calc(var(--vh, 1vh) * 100) !important;\n}\n\n// Fixed widths\n.width-50 {\n  width: 50px !important;\n}\n\n.width-100 {\n  width: 100px !important;\n}\n\n.width-150 {\n  width: 150px !important;\n}\n\n.width-200 {\n  width: 200px !important;\n}\n\n.width-250 {\n  width: 250px !important;\n}\n\n.width-300 {\n  width: 300px !important;\n}\n\n.width-350 {\n  width: 350px !important;\n}\n\n.width-400 {\n  width: 400px !important;\n}\n\n.width-450 {\n  width: 450px !important;\n}\n\n.width-500 {\n  width: 500px !important;\n}\n\n.width-550 {\n  width: 550px !important;\n}\n\n.width-600 {\n  width: 600px !important;\n}\n\n.width-650 {\n  width: 650px !important;\n}\n\n.width-700 {\n  width: 700px !important;\n}\n\n.width-750 {\n  width: 750px !important;\n}\n\n.width-800 {\n  width: 800px !important;\n}\n\n// Width in %\n.width-5-per {\n  width: 5% !important;\n}\n\n.width-10-per {\n  width: 10% !important;\n}\n\n.width-15-per {\n  width: 15% !important;\n}\n\n.width-20-per {\n  width: 20% !important;\n}\n\n.width-25-per {\n  width: 25% !important;\n}\n\n.width-30-per {\n  width: 30% !important;\n}\n\n.width-35-per {\n  width: 35% !important;\n}\n\n.width-40-per {\n  width: 40% !important;\n}\n\n.width-45-per {\n  width: 45% !important;\n}\n\n.width-50-per {\n  width: 50% !important;\n}\n\n.width-55-per {\n  width: 55% !important;\n}\n\n.width-60-per {\n  width: 60% !important;\n}\n\n.width-65-per {\n  width: 65% !important;\n}\n\n.