 }\n\n  &[data-background-color=\"king-yna\"] {\n    @include set-gradient-background-color(map-get($primary-color, darken-2), map-get($danger-color, base));\n  }\n\n  &[data-background-color=\"ibiza-sunset\"] {\n    @include set-gradient-background-color(map-get($danger-color, darken-3), map-get($warning-color, lighten-1));\n  }\n\n  &[data-background-color=\"flickr\"] {\n    @include set-gradient-background-color(map-get($warning-color, darken-1), map-get($info-color, darken-1));\n  }\n\n  &[data-background-color=\"purple-bliss\"] {\n    @include set-gradient-background-color(map-get($primary-color, darken-3), map-get($info-color, base));\n  }\n\n  &[data-background-color=\"man-of-steel\"] {\n    @include set-gradient-background-color(#2F3EA0, #AE342D);\n  }\n\n  &[data-background-color=\"purple-love\"] {\n    @include set-gradient-background-color(map-get($primary-color, darken-2), map-get($primary-color, lighten-2));\n  }\n\n  // Native Scrollbar (.menu-native-scroll)\n  &.menu-native-scroll {\n    .main-menu-content {\n      overflow-y: scroll;\n    }\n  }\n}\n\n.sidenav-overlay {\n  position: fixed;\n  top: 0;\n  left: 0;\n  right: 0;\n  height: calc(var(--vh, 1vh) * 100);\n  background-color: rgba(0, 0, 0, .5);\n  z-index: 997;\n  display: none;\n}\n\n.drag-target {\n  height: 100%;\n  width: 40px;\n  position: fixed;\n  top: 0;\n  left: -20px;\n  z-index: 1036;\n}\n\n.sidebar-header {\n  a.nav-toggle {\n    display: inline-block;\n    position: absolute;\n    width: 20px;\n    height: 20px;\n    line-height: 20px;\n    text-align: center;\n    right: 14px;\n    top: 25px;\n    color: rgba($white, 0.55);\n  }\n\n  a.nav-close {\n    display: inline-block;\n    position: absolute;\n    width: 20px;\n    height: 20px;\n    line-height: 20px;\n    text-align: center;\n    right: 14px;\n    top: 24px;\n    color: $white;\n  }\n}\n\n.main-panel {\n  position: relative;\n  z-index: 2;\n  float: right;\n  transition: 0.33s, cubic-bezier(0.685, 0.0473, 0.346, 1);\n\n  .main-content {\n    position: relative;\n    -webkit-transition: all 0.3s ease;\n    transition: all 0.3s ease;\n    min-height: calc((var(--vh, 1vh) * 100) - 7.39rem);\n  }\n\n  div {\n    .footer {\n      border-top: 1px solid map-get($grey-color, lighten-3);\n    }\n  }\n\n  >.navbar {\n    margin-bottom: 0;\n  }\n}\n\n.bootstrap-navbar .navigation {\n  margin-top: 20px;\n  float: none;\n}\n\n.main-panel {\n  width: 100%;\n}\n\n.sidebar-sm {\n\n  &.menu-expanded,\n  &.menu-open {\n    .app-sidebar {\n      width: $menu-expanded-width-sm;\n      left: 0;\n    }\n  }\n}\n\n.sidebar-lg {\n\n  &.menu-expanded,\n  &.menu-open {\n    .app-sidebar {\n      width: $menu-expanded-width-lg;\n      left: 0;\n    }\n  }\n}\n\n@include media-breakpoint-up(xl) {\n  .app-sidebar {\n    left: 0;\n    width: $menu-expanded-width;\n  }\n}\n\n@include media-breakpoint-down(md) {\n  body.nav-collapsed {\n\n    &:not(.sidebar-sm),\n    &:not(.sidebar-lg) {\n      .navbar {\n        margin-left: 0;\n      }\n\n      .main-panel .main-content {\n        padding-left: 0;\n      }\n    }\n  }\n}\n\n@include media-breakpoint-up(xl) {\n  .drag-target {\n    z-index: 0;\n  }\n\n  body.nav-collapsed {\n    .app-sidebar {\n      width: $menu-collapsed-width;\n    }\n\n    &:not(.sidebar-sm):not(.sidebar-lg) .app-sidebar {\n      &:hover {\n        width: $menu-expanded-width;\n      }\n    }\n\n    &.sidebar-sm .app-sidebar {\n      &:hover {\n        width: $menu-expanded-width-sm;\n      }\n    }\n\n    &.sidebar-lg .app-sidebar {\n      &:hover {\n        width: $menu-expanded-width-lg;\n      }\n    }\n\n    &:not(.horizontal-layout) {\n      .navbar {\n        margin-left: $menu-collapsed-width;\n      }\n\n      .main-panel {\n        .main-content {\n          margin-left: $menu-collapsed-width;\n        }\n      }\n\n      footer.footer {\n        margin-left: $menu-collapsed-width;\n      }\n    }\n  }\n}\n\n@include media-breakpoint-down(lg) {\n  .app-sidebar {\n    transform: translate3d(-100%, 0, 0);\n  }\n}\n","@function calc-top($line-height-base, $font-size, $component-height) {\n  @return (($line-height-base * $font-size) - $component-height) / 2; // vertical center of line-height\n}\n\n// Emulate the less #contrast function\n// TODO: this may be useful for the inverse theme, but if not, remove (it is unused after the removal of fullpalette)\n// contrast-color and brightness borrowed from compass\n// Copyright (c) 2009-2014 Christopher M. Eppstein\n// Complies with license: https://github.com/Compass/compass/blob/stable/LICENSE.markdown\n@function contrast-color($color, $dark: $contrasted-dark-default, $light: $contrasted-light-default, $threshold: null) {\n  @if $threshold {\n    // Deprecated in Compass 0.13\n    @warn \"The $threshold argment to contrast-color is no longer needed and will be removed in the next release.\";\n  }\n\n  @if $color == null {\n    @return null;\n  } @else {\n    $color-brightness: brightness($color);\n    $dark-text-brightness: brightness($dark);\n    $light-text-brightness: brightness($light);\n    @return if(abs($color-brightness - $light-text-brightness) > abs($color-brightness - $dark-text-brightness), $light, $dark);\n  }\n}\n\n@function brightness($color) {\n  @if type-of($color) == color {\n    @return (red($color) * 0.299 + green($color) * 0.587 + blue($color) * 0.114) / 255 * 100%;\n  } @else {\n    @return unquote(\"brightness(#{$color})\");\n  }\n}\n\n@mixin set-background-color($color){\n  background-color: $color;\n  @include set-sidebar-background($color);\n  @include set-font-icon-color($color);\n}\n\n@mixin set-active-color($color){\n  background-color: $color;\n}\n\n@mixin set-sidebar-background($color){\n  $overlay-opacity: .95;\n  @if $color == $black {\n    $overlay-opacity: .85;\n  }\n  @if $color == $white {\n    $overlay-opacity: .9;\n  }\n\n  .sidebar-background{\n    &:after{\n      background: $color;\n      opacity: $overlay-opacity;\n    }\n  } \n}\n\n@mixin set-gradient-background-color($start-color, $end-color){\n  @include gradient-directional($start-color, $end-color, $deg: 135deg);\n  $overlay-opacity: .95;\n  .sidebar-background{\n    &:after{\n      @include gradient-directional($start-color, $end-color, $deg: 135deg);\n      opacity: $overlay-opacity;\n    }\n  }\n  @include set-font-icon-color($start-color);\n}\n\n@mixin set-font-icon-color($color){\n  $sidebar-font-color : $white;\n  @if $color == $white {\n    $sidebar-font-color: $black;\n  }\n\n  .navigation i, .navigation li > a{\n    color: $sidebar-font-color;\n  }\n\n  .logo-text{\n    color: $sidebar-font-color;\n  }\n\n  .sidebar-header a.nav-toggle{\n    color: $sidebar-font-color;\n  }\n\n  .sidebar-header a.nav-close{\n    color: $sidebar-font-color;\n  }\n}\n\n@mixin set-anchor-color($color){\n  .navigation{\n    li{\n      a{\n        color: $color;\n      }\n    }\n  }\n}\n@mixin set-logo-text-color($color){\n  .logo-text{\n    color: $color;\n  }\n  .sidebar-header a.nav-toggle{\n    color: $color;\n  }\n}","@import \"../core/colors/palette-variables\";\n\n// notofication sidebar that slide In from right\n#notification-sidebar{\n\twidth: 400px;\n  right: -405px;\n  padding: 0;\n  background-color: $white;\n  z-index: 1051;\n  position: fixed;\n  top: 0;\n  bottom: 0;\n  height: 100vh;\n  height: calc(var(--vh, 1vh) * 100);\n  transition: right 0.6s cubic-bezier(0.05, 0.74, 0.2, 0.99);\n  backface-visibility: hidden;\n\n\t&.open{\n\t\tright: 0;\n\t\t@include card-shadow();\n\t}\n\n\t.notification-sidebar-close{\n\t\tposition: absolute;\n\t    right: 10px;\n\t    top: 10px;\n\t    padding: 7px;\n\t    width: auto;\n\t    z-index: 10;\n\t}\n\n\t.notification-sidebar-content{\n\t\tposition: relative;\n    \theight: 100%;\n    \tpadding: 10px;\n\n\t\t.timeline-left{\n\t\t\tpadding-left: 1.3rem;\n\t\t\t.timeline-line {\n\t\t\t\twidth: 1px;\n\t\t\t\tmargin-bottom: 4rem;\n\t\t\t}\n\t\t\t.timeline-item{\n\t\t\t\tpadding-bottom: 1rem;\n\t\t\t\t&:after{\n\t\t\t\t\tborder:none;\n\t\t\t\t}\n\t\t\t\t.activity-list-text {\n\t\t\t\t\tmargin-top: 0.7rem;\n\t\t\t\t\tpadding-left: 1rem;\n\t\t\t\t\tpadding-right: 1rem;\n\t\t\t\t\t.notification-note {\n\t\t\t\t\t\tbackground-color: map-get($grey-color, lighten-4);\n\t\t\t\t\t\tborder-radius: $border-radius;\n\t\t\t\t\t}\n\t\t\t\t}\n\t\t\t}\n\t\t}\n\t\t.notification-tab-content {\n\t\t\tposition: relative;\n\t\t\theight: calc(100vh - 3.333333rem);\n\t\t\theight: calc(var(--vh, 1vh)*100 - 3.333333rem);\n\t\t\t.ps__rail-y {\n\t\t\t\tright: 5px;\n\t\t\t}\n\t\t}\n\t}\n}\n","@import \"../core/variables/variables\";\n@import \"../core/variables/app-variables\";\n@import \"../core/colors/palette-variables\";\n\n// Full page background\n.auth-page:not(.layout-dark) {\n  background-image: linear-gradient(43deg, rgba(105, 105, 105, 0.02) 0%, rgba(105, 105, 105, 0.02) 62%, rgba(227, 227, 227, 0.02) 62%, rgba(227, 227, 227, 0.02) 69%, rgba(24, 24, 24, 0.02) 69%, rgba(24, 24, 24, 0.02) 80%, rgba(13, 13, 13, 0.02) 80%, rgba(13, 13, 13, 0.02) 82%, rgba(13, 13, 13, 0.02) 82%, rgba(13, 13, 13, 0.02) 100%), linear-gradient(31deg, rgba(155, 155, 155, 0.02) 0%, rgba(155, 155, 155, 0.02) 29%, rgba(164, 164, 164, 0.02) 29%, rgba(164, 164, 164, 0.02) 41%, rgba(200, 200, 200, 0.02) 41%, rgba(200, 200, 200, 0.02) 74%, rgba(229, 229, 229, 0.02) 74%, rgba(229, 229, 229, 0.02) 79%, rgba(15, 15, 15, 0.02) 79%, rgba(15, 15, 15, 0.02) 100%), linear-gradient(319deg, rgba(39, 39, 39, 0.02) 0%, rgba(39, 39, 39, 0.02) 17%, rgba(49, 49, 49, 0.02) 17%, rgba(49, 49, 49, 0.02) 25%, rgba(59, 59, 59, 0.02) 25%, rgba(59, 59, 59, 0.02) 32%, rgba(194, 194, 194, 0.02) 32%, rgba(194, 194, 194, 0.02) 46%, rgba(220, 220, 220, 0.02) 46%, rgba(220, 220, 220, 0.02) 100%), linear-gradient(113deg, rgba(188, 188, 188, 0.02) 0%, rgba(188, 188, 188, 0.02) 15%, rgba(223, 223, 223, 0.02) 15%, rgba(223, 223, 223, 0.02) 46%, rgba(21, 21, 21, 0.02) 46%, rgba(21, 21, 21, 0.02) 88%, rgba(93, 93, 93, 0.02) 88%, rgba(93, 93, 93, 0.02) 94%, rgba(130, 130, 130, 0.02) 94%, rgba(130, 130, 130, 0.02) 100%), linear-gradient(29deg, rgba(134, 134, 134, 0.02) 0%, rgba(134, 134, 134, 0.02) 27%, rgba(181, 181, 181, 0.02) 27%, rgba(181, 181, 181, 0.02) 41%, rgba(81, 81, 81, 0.02) 41%, rgba(81, 81, 81, 0.02) 46%, rgba(253, 253, 253, 0.02) 46%, rgba(253, 253, 253, 0.02) 58%, rgba(74, 74, 74, 0.02) 58%, rgba(74, 74, 74, 0.02) 100%), linear-gradient(90deg, rgb(229, 229, 229), rgb(228, 228, 228));\n  background-size: cover;\n  background-repeat: no-repeat;\n}\n\n.auth-page {\n  .auth-img {\n    padding: 0;\n\n    .auth-img-bg {\n      background-color: map-get($grey-color, lighten-4);\n    }\n  }\n}\n\n// to remove scrollbar in auth pages\n.auth-height {\n  overflow: auto;\n}\n","@import \"../core/variables/variables\";\n@import \"../core/variables/app-variables\";\n\n.faq-wrapper {\n  .faq-bg {\n    background: url(../../../assets/img/pages/faq.png) repeat !important;\n  }\n  .faq-search {\n    // search input\n    .faq-search-input {\n      margin-bottom: 1.25rem;\n      input {\n        background-color: $white;\n      }\n    }\n    @include media-breakpoint-down(md) {\n      .faq-search-input {\n        width: 75% !important;\n      }\n    }\n    @include media-breakpoint-down(sm) {\n      .faq-search-input {\n        width: 100% !important;\n      }\n    }\n    // search button\n    button {\n      right: 0.4rem;\n      top: 0.36rem;\n    }\n    @include media-breakpoint-down(xs) {\n      button {\n        padding: 0.65rem 1.3rem;\n      }\n    }\n  }\n  // Slide content show/hide\n  .faq {\n    .wrapper-content{\n      display: none;\n      &.active{\n        display: block;\n      }\n      .collapse-icon.accordion-icon-rotate:not(.left) .card-header {\n        padding-right: 3.5rem !important;\n      }\n    }\n  }\n  @include media-breakpoint-down(xs) {\n    .faq-title{\n      font-size: $h2-font-size;\n    }\n    .faq-subtitle{\n      font-size: $h4-font-size;\n    }\n  }\n}\n","// ================================================================================================\n// \tFile Name: gallery.scss\n// \tDescription: Page content different types of gallery page layouts SCSS.\n// \t----------------------------------------------------------------------------------------------\n// \tItem Name: Apex - Responsive Admin Theme\n// \tAuthor: PIXINVENT\n// \tAuthor URL: http://www.themeforest.net/user/pixinvent\n// ================================================================================================\n@import \"../core/variables/variables\";\n@import \"../core/variables/app-variables\";\n@import \"../core/colors/palette-variables\";\n\n.gallery-thumbnail {\n  max-width: 100%;\n  height: auto;\n}\n\n.grid-sizer,\n.grid-item {\n  width: 20%;\n}\n\n.grid-item {\n\n  figure,\n  >div {\n    margin: 0;\n    margin: 0.4rem;\n  }\n}\n\n/*  masonry grid media queries */\n@media screen and (max-width: 2200px) {\n\n  .grid-sizer,\n  .grid-item {\n    width: 20%;\n  }\n}\n\n@media screen and (max-width: 1800px) {\n\n  .grid-sizer,\n  .grid-item {\n    width: 30%;\n  }\n}\n\n@media screen and (max-width: 1600px) {\n\n  .grid-sizer,\n  .grid-item {\n    width: 33.33%;\n  }\n}\n\n@media screen and (max-width: 1224px) {\n\n  .grid-sizer,\n  .grid-item {\n    width: 33.33%;\n  }\n}\n\n@media screen and (max-width: 980px) {\n\n  .grid-sizer,\n  .grid-item {\n    width: 50%;\n  }\n}\n\n@media screen and (max-width: 720px) {\n\n  .grid-sizer,\n  .grid-item {\n    width: 50%;\n  }\n}\n\n@media screen and (max-width: 480px) {\n\n  .grid-sizer,\n  .grid-item {\n    width: 100%;\n  }\n}\n\n\n/* Gallery hover effects */\n\n.grid-hover {\n  position: relative;\n  margin: 0 auto;\n  padding: 1em 0 4em;\n  list-style: none;\n\n  figure {\n    position: relative;\n    overflow: hidden;\n    margin: 10px 1%;\n    min-width: auto;\n    max-width: 480px;\n    max-height: 360px;\n    background: map-get($info-color, lighten-1);\n    text-align: center;\n    cursor: pointer;\n\n    img {\n      position: relative;\n      display: block;\n      min-height: 100%;\n      max-width: 100%;\n      opacity: 0.8;\n    }\n\n    figcaption {\n      padding: 2em;\n      color: $white;\n      text-transform: uppercase;\n      font-size: 1.25em;\n      -webkit-backface-visibility: hidden;\n      backface-visibility: hidden;\n      position: absolute;\n      top: 0;\n      left: 0;\n      width: 100%;\n      height: 100%;\n\n      &::before {\n        pointer-events: none;\n      }\n\n      &::after {\n        pointer-events: none;\n      }\n\n      >a {\n        position: absolute;\n        top: 0;\n        left: 0;\n        width: 100%;\n        height: 100%;\n        z-index: 1000;\n        text-indent: 200%;\n        white-space: nowrap;\n        font-size: 0;\n        opacity: 0;\n      }\n    }\n\n    h2 {\n      word-spacing: -0.15em;\n      font-weight: 300;\n      margin: 0;\n\n      span {\n        font-weight: 800;\n      }\n    }\n\n    p {\n      margin: 0;\n      letter-spacing: 1px;\n      font-size: 68.5%;\n    }\n  }\n}\n\n\n/*---------------*/\n/***** Lily *****/\n/*---------------*/\nfigure.effect-lily {\n  img {\n    max-width: none;\n    width: -webkit-calc(100% + 50px);\n    width: calc(100% + 50px);\n    opacity: 0.7;\n    transition: opacity 0.35s, transform 0.35s;\n    transform: translate3d(-40px, 0, 0);\n  }\n\n  figcaption {\n    text-align: left;\n\n    >div {\n      position: absolute;\n      bottom: 0;\n      left: 0;\n      padding: 2em;\n      width: 100%;\n      height: 50%;\n    }\n  }\n\n  h2 {\n    transform: translate3d(0, 40px, 0);\n    transition: transform 0.35s;\n  }\n\n  p {\n    transform: translate3d(0, 40px, 0);\n    color: rgba($white, 0.8);\n    opacity: 0;\n    transition: opacity 0.2s, transform 0.35s;\n  }\n\n  &:hover {\n    img {\n      opacity: 1;\n      transform: translate3d(0, 0, 0);\n    }\n\n    p {\n      opacity: 1;\n      transform: translate3d(0, 0, 0);\n      -webkit-transition-delay: 0.05s;\n      transition-delay: 0.05s;\n      -webkit-transition-duration: 0.35s;\n      transition-duration: 0.35s;\n    }\n\n    h2 {\n      transform: translate3d(0, 0, 0);\n    }\n  }\n}\n\n/*---------------*/\n/***** Sadie *****/\n/*---------------*/\n\nfigure.effect-sadie {\n  figcaption {\n    &::before {\n      position: absolute;\n      top: 0;\n      left: 0;\n      width: 100%;\n      height: 100%;\n      background: -webkit-linear-gradient(top, rgba(72, 76, 97, 0) 0%, rgba(72, 76, 97, 0.8) 75%);\n      background: linear-gradient(to bottom, rgba(72, 76, 97, 0) 0%, rgba(72, 76, 97, 0.8) 75%);\n      content: '';\n      opacity: 0;\n      transform: translate3d(0, 50%, 0);\n      transition: opacity 0.35s, transform 0.35s;\n    }\n  }\n\n  h2 {\n    position: absolute;\n    top: 50%;\n    left: 0;\n    width: 100%;\n    transition: transform 0.35s, color 0.35s;\n    transform: translate3d(0, -50%, 0);\n  }\n\n  p {\n    transition: opacity 0.35s, transform 0.35s;\n    position: absolute;\n    bottom: 0;\n    left: 0;\n    padding: 2em;\n    width: 100%;\n    opacity: 0;\n    transform: translate3d(0, 10px, 0);\n  }\n\n  &:hover {\n    h2 {\n      color: $white;\n      transform: translate3d(0, -50%, 0) translate3d(0, -40px, 0);\n    }\n\n    figcaption {\n      &::before {\n        opacity: 1;\n        transform: translate3d(0, 0, 0);\n      }\n    }\n\n    p {\n      opacity: 1;\n      transform: translate3d(0, 0, 0);\n    }\n  }\n}\n\n/*---------------*/\n/***** Roxy *****/\n/*---------------*/\nfigure.effect-roxy {\n  background: map-get($info-color, base);\n\n  img {\n    max-width: none;\n    width: -webkit-calc(100% + 60px);\n    width: calc(100% + 60px);\n    transition: opacity 0.35s, transform 0.35s;\n    transform: translate3d(-50px, 0, 0);\n  }\n\n  figcaption {\n    &::before {\n      position: absolute;\n      top: 30px;\n      right: 30px;\n      bottom: 30px;\n      left: 30px;\n      border: 1px solid $white;\n      content: '';\n      opacity: 0;\n      transition: opacity 0.35s, transform 0.35s;\n      transform: translate3d(-20px, 0, 0);\n    }\n\n    padding: 3em;\n    text-align: left;\n  }\n\n  h2 {\n    padding: 30% 0 10px 0;\n  }\n\n  p {\n    opacity: 0;\n    transition: opacity 0.35s, transform 0.35s;\n    transform: translate3d(-10px, 0, 0);\n  }\n\n  &:hover {\n    img {\n      opacity: 0.7;\n      transform: translate3d(0, 0, 0);\n    }\n\n    figcaption {\n      &::before {\n        opacity: 1;\n        transform: translate3d(0, 0, 0);\n      }\n    }\n\n    p {\n      opacity: 1;\n      transform: translate3d(0, 0, 0);\n    }\n  }\n}\n\n/*---------------*/\n/***** Bubba *****/\n/*---------------*/\nfigure.effect-bubba {\n  background: map-get($warning-color, darken-1);\n\n  img {\n    opacity: 0.7;\n    transition: opacity 0.35s;\n  }\n\n  &:hover {\n    img {\n      opacity: 0.4;\n    }\n\n    figcaption {\n      &::before {\n        opacity: 1;\n        transform: scale(1);\n      }\n\n      &::after {\n        opacity: 1;\n        transform: scale(1);\n      }\n    }\n\n    h2 {\n      opacity: 1;\n      transform: translate3d(0, 0, 0);\n    }\n\n    p {\n      opacity: 1;\n      transform: translate3d(0, 0, 0);\n    }\n  }\n\n  figcaption {\n    &::before {\n      position: absolute;\n      top: 30px;\n      right: 30px;\n      bottom: 30px;\n      left: 30px;\n      content: '';\n      opacity: 0;\n      transition: opacity 0.35s, transform 0.35s;\n      border-top: 1px solid $white;\n      border-bottom: 1px solid $white;\n      transform: scale(0, 1);\n    }\n\n    &::after {\n      position: absolute;\n      top: 30px;\n      right: 30px;\n      bottom: 30px;\n      left: 30px;\n      content: '';\n      opacity: 0;\n      transition: opacity 0.35s, transform 0.35s;\n      border-right: 1px solid $white;\n      border-left: 1px solid $white;\n      transform: scale(1, 0);\n    }\n  }\n\n  h2 {\n    padding-top: 30%;\n    transition: transform 0.35s;\n    transform: translate3d(0, -20px, 0);\n  }\n\n  p {\n    padding: 20px 2.5em;\n    opacity: 0;\n    transition: opacity 0.35s, transform 0.35s;\n    transform: translate3d(0, 20px, 0);\n  }\n}\n\n/*---------------*/\n/***** Romeo *****/\n/*---------------*/\nfigure.effect-romeo {\n  -webkit-perspective: 1000px;\n  perspective: 1000px;\n\n  img {\n    transition: opacity 0.35s, transform 0.35s;\n    transform: translate3d(0, 0, 300px);\n  }\n\n  &:hover {\n    img {\n      opacity: 0.6;\n      transform: translate3d(0, 0, 0);\n    }\n\n    figcaption {\n      &::before {\n        opacity: 0.5;\n        transform: translate3d(-50%, -50%, 0) rotate(45deg);\n      }\n\n      &::after {\n        opacity: 0.5;\n        transform: translate3d(-50%, -50%, 0) rotate(-45deg);\n      }\n    }\n\n    h2 {\n      transform: translate3d(0, -50%, 0) translate3d(0, -100%, 0);\n    }\n\n    p {\n      transform: translate3d(0, -50%, 0) translate3d(0, 100%, 0);\n    }\n  }\n\n  figcaption {\n    &::before {\n      position: absolute;\n      top: 50%;\n      left: 50%;\n      width: 80%;\n      height: 1px;\n      background: $white;\n      content: '';\n      transition: opacity 0.35s, transform 0.35s;\n      transform: translate3d(-50%, -50%, 0);\n    }\n\n    &::after {\n      position: absolute;\n      top: 50%;\n      left: 50%;\n      width: 80%;\n      height: 1px;\n      background: $white;\n      content: '';\n      transition: opacity 0.35s, transform 0.35s;\n      transform: translate3d(-50%, -50%, 0);\n    }\n  }\n\n  h2 {\n    position: absolute;\n    top: 50%;\n    left: 0;\n    width: 100%;\n    transition: transform 0.35s;\n    transform: translate3d(0, -50%, 0) translate3d(0, -150%, 0);\n  }\n\n  p {\n    position: absolute;\n    top: 50%;\n    left: 0;\n    width: 100%;\n    transition: transform 0.35s;\n    padding: 0.25em 2em;\n    transform: translate3d(0, -50%, 0) translate3d(0, 150%, 0);\n  }\n}\n\n/*---------------*/\n/***** Layla *****/\n/*---------------*/\nfigure.effect-layla {\n  background: map-get($success-color, darken-1);\n\n  img {\n    height: 390px;\n    transform: translate3d(0, -30px, 0);\n    transition: opacity 0.35s, transform 0.35s;\n  }\n\n  figcaption {\n    padding: 3em;\n\n    &::before {\n      position: absolute;\n      content: '';\n      opacity: 0;\n      top: 50px;\n      right: 30px;\n      bottom: 50px;\n      left: 30px;\n      border-top: 1px solid $white;\n      border-bottom: 1px solid $white;\n      transform: scale(0, 1);\n      -webkit-transform-origin: 0 0;\n      transform-origin: 0 0;\n      transition: opacity 0.35s, transform 0.35s;\n    }\n\n    &::after {\n      position: absolute;\n      content: '';\n      opacity: 0;\n      top: 30px;\n      right: 50px;\n      bottom: 30px;\n      left: 50px;\n      border-right: 1px solid $white;\n      border-left: 1px solid $white;\n      transform: scale(1, 0);\n      -webkit-transform-origin: 100% 0;\n      transform-origin: 100% 0;\n      transition: opacity 0.35s, transform 0.35s;\n    }\n  }\n\n  h2 {\n    padding-top: 26%;\n    transition: transform 0.35s;\n    transform: translate3d(0, -30px, 0);\n  }\n\n  p {\n    padding: 0.5em 2em;\n    text-transform: none;\n    opacity: 0;\n    transform: translate3d(0, -10px, 0);\n    transition: opacity 0.35s, transform 0.35s;\n  }\n\n  &:hover {\n    img {\n      opacity: 0.7;\n      transform: translate3d(0, 0, 0);\n      -webkit-transition-delay: 0.15s;\n      transition-delay: 0.15s;\n    }\n\n    figcaption {\n      &::before {\n        opacity: 1;\n        transform: scale(1);\n      }\n\n      &::after {\n        opacity: 1;\n        transform: scale(1);\n        -webkit-transition-delay: 0.15s;\n        transition-delay: 0.15s;\n      }\n    }\n\n    h2 {\n      opacity: 1;\n      transform: translate3d(0, 0, 0);\n      -webkit-transition-delay: 0.15s;\n      transition-delay: 0.15s;\n    }\n\n    p {\n      opacity: 1;\n      transform: translate3d(0, 0, 0);\n      -webkit-transition-delay: 0.15s;\n      transition-delay: 0.15s;\n    }\n  }\n}\n\n/*---------------*/\n/***** Honey *****/\n/*---------------*/\nfigure.effect-honey {\n  background: map-get($secondary-color, darken-2);\n\n  img {\n    opacity: 0.9;\n    transition: opacity 0.35s;\n  }\n\n  &:hover {\n    img {\n      opacity: 0.5;\n    }\n\n    figcaption {\n      &::before {\n        opacity: 1;\n        transform: translate3d(0, 0, 0);\n      }\n    }\n\n    h2 {\n      opacity: 1;\n      transform: translate3d(0, 0, 0);\n\n      i {\n        opacity: 1;\n        transform: translate3d(0, 0, 0);\n      }\n    }\n  }\n\n  figcaption {\n    &::before {\n      position: absolute;\n      bottom: 0;\n      left: 0;\n      width: 100%;\n      height: 10px;\n      background: $white;\n      content: '';\n      transform: translate3d(0, 10px, 0);\n      transition: transform 0.35s;\n    }\n  }\n\n  h2 {\n    position: absolute;\n    bottom: 0;\n    left: 0;\n    padding: 1em 1.5em;\n    width: 100%;\n    text-align: left;\n    transform: translate3d(0, -30px, 0);\n    transition: transform 0.35s;\n\n    i {\n      font-style: normal;\n      opacity: 0;\n      transition: opacity 0.35s, transform 0.35s;\n      transform: translate3d(0, -30px, 0);\n    }\n  }\n}\n\n/*---------------*/\n/***** Oscar *****/\n/*---------------*/\nfigure.effect-oscar {\n  @include gradient-directional(map-get($danger-color, darken-4), map-get($warning-color, base));\n\n  img {\n    opacity: 0.9;\n    transition: opacity 0.35s;\n  }\n\n  figcaption {\n    padding: 3em;\n    background-color: rgba(58, 52, 42, 0.7);\n    transition: background-color 0.35s;\n\n    &::before {\n      position: absolute;\n      top: 30px;\n      right: 30px;\n      bottom: 30px;\n      left: 30px;\n      border: 1px solid $white;\n      content: '';\n      opacity: 0;\n      transition: opacity 0.35s, transform 0.35s;\n      transform: scale(0);\n    }\n  }\n\n  h2 {\n    margin: 20% 0 10px 0;\n    transition: transform 0.35s;\n    transform: translate3d(0, 100%, 0);\n  }\n\n  p {\n    opacity: 0;\n    transition: opacity 0.35s, transform 0.35s;\n    transform: scale(0);\n  }\n\n  &:hover {\n    h2 {\n      transform: translate3d(0, 0, 0);\n    }\n\n    figcaption {\n      &::before {\n        opacity: 1;\n        transform: scale(1);\n      }\n\n      background-color: rgba(58, 52, 42, 0);\n    }\n\n    p {\n      opacity: 1;\n      transform: scale(1);\n    }\n\n    img {\n      opacity: 0.4;\n    }\n  }\n}\n\n/*---------------*/\n/***** Marley *****/\n/*---------------*/\nfigure.effect-marley {\n  figcaption {\n    text-align: right;\n  }\n\n  h2 {\n    position: absolute;\n    right: 30px;\n    left: 30px;\n    padding: 10px 0;\n    top: 30px;\n    transition: transform 0.35s;\n    transform: translate3d(0, 20px, 0);\n\n    &::after {\n      position: absolute;\n      top: 100%;\n      left: 0;\n      width: 100%;\n      height: 4px;\n      background: $white;\n      content: '';\n      transform: translate3d(0, 40px, 0);\n      opacity: 0;\n      transition: opacity 0.35s, transform 0.35s;\n    }\n  }\n\n  p {\n    position: absolute;\n    right: 30px;\n    left: 30px;\n    padding: 10px 0;\n    bottom: 30px;\n    line-height: 1.5;\n    transform: translate3d(0, 100%, 0);\n    opacity: 0;\n    transition: opacity 0.35s, transform 0.35s;\n  }\n\n  &:hover {\n    h2 {\n      transform: translate3d(0, 0, 0);\n\n      &::after {\n        opacity: 1;\n        transform: translate3d(0, 0, 0);\n      }\n    }\n\n    p {\n      opacity: 1;\n      transform: translate3d(0, 0, 0);\n    }\n  }\n}\n\n/*---------------*/\n/***** Ruby *****/\n/*---------------*/\nfigure.effect-ruby {\n  background-color: map-get($info-color, base);\n\n  img {\n    opacity: 0.7;\n    transition: opacity 0.35s, transform 0.35s;\n    transform: scale(1.15);\n  }\n\n  &:hover {\n    img {\n      opacity: 0.5;\n      transform: scale(1);\n    }\n\n    h2 {\n      transform: translate3d(0, 0, 0);\n    }\n\n    p {\n      opacity: 1;\n      transform: translate3d(0, 0, 0) scale(1);\n    }\n  }\n\n  h2 {\n    margin-top: 20%;\n    transition: transform 0.35s;\n    transform: translate3d(0, 20px, 0);\n  }\n\n  p {\n    margin: 1em 0 0;\n    padding: 3em;\n    border: 1px solid $white;\n    opacity: 0;\n    transition: opacity 0.35s, transform 0.35s;\n    transform: translate3d(0, 20px, 0) scale(1.1);\n  }\n}\n\n/*---------------*/\n/***** Milo *****/\n/*---------------*/\nfigure.effect-milo {\n  background: map-get($success-color, darken-4);\n\n  img {\n    max-width: none;\n    width: -webkit-calc(100% + 60px);\n    width: calc(100% + 60px);\n    opacity: 1;\n    transition: opacity 0.35s, transform 0.35s;\n    transform: translate3d(-30px, 0, 0) scale(1.12);\n    -webkit-backface-visibility: hidden;\n    backface-visibility: hidden;\n  }\n\n  &:hover {\n    img {\n      opacity: 0.5;\n      transform: translate3d(0, 0, 0) scale(1);\n    }\n\n    p {\n      opacity: 1;\n      transform: translate3d(0, 0, 0);\n    }\n  }\n\n  h2 {\n    position: absolute;\n    right: 0;\n    bottom: 0;\n    padding: 1em 1.2em;\n  }\n\n  p {\n    padding: 0 10px 0 0;\n    width: 50%;\n    border-right: 1px solid $white;\n    text-align: right;\n    opacity: 0;\n    transition: opacity 0.35s, transform 0.35s;\n    transform: translate3d(-40px, 0, 0);\n  }\n}\n\n/*---------------*/\n/***** Dexter *****/\n/*---------------*/\nfigure.effect-dexter {\n  background: -webkit-linear-gradient(top, rgba(37, 141, 200, 1) 0%, rgba(104, 60, 19, 1) 100%);\n  background: linear-gradient(to bottom, rgba(37, 141, 200, 1) 0%, rgba(104, 60, 19, 1) 100%);\n\n  img {\n    transition: opacity 0.35s;\n  }\n\n  &:hover {\n    img {\n      opacity: 0.4;\n    }\n\n    figcaption {\n      &::after {\n        transform: translate3d(0, 0, 0);\n      }\n    }\n\n    p {\n      opacity: 1;\n      transform: translate3d(0, 0, 0);\n    }\n  }\n\n  figcaption {\n    &::after {\n      position: absolute;\n      right: 30px;\n      bottom: 30px;\n      left: 30px;\n      height: -webkit-calc(50% - 30px);\n      height: calc(50% - 30px);\n      border: 7px solid $white;\n      content: '';\n      transition: transform 0.35s;\n      transform: translate3d(0, -100%, 0);\n    }\n\n    padding: 3em;\n    text-align: left;\n  }\n\n  p {\n    position: absolute;\n    right: 60px;\n    bottom: 60px;\n    left: 60px;\n    opacity: 0;\n    transition: opacity 0.35s, transform 0.35s;\n    transform: translate3d(0, -100px, 0);\n  }\n}\n\n/*---------------*/\n/***** Sarah *****/\n/*---------------*/\nfigure.effect-sarah {\n  background: map-get($success-color, darken-2);\n\n  img {\n    max-width: none;\n    width: -webkit-calc(100% + 20px);\n    width: calc(100% + 20px);\n    transition: opacity 0.35s, transform 0.35s;\n    transform: translate3d(-10px, 0, 0);\n    -webkit-backface-visibility: hidden;\n    backface-visibility: hidden;\n  }\n\n  &:hover {\n    img {\n      opacity: 0.4;\n      transform: translate3d(0, 0, 0);\n    }\n\n    h2 {\n      &::after {\n        transform: translate3d(0, 0, 0);\n      }\n    }\n\n    p {\n      opacity: 1;\n      transform: translate3d(0, 0, 0);\n    }\n  }\n\n  figcaption {\n    text-align: left;\n  }\n\n  h2 {\n    position: relative;\n    overflow: hidden;\n    padding: 0.5em 0;\n\n    &::after {\n      position: absolute;\n      bottom: 0;\n      left: 0;\n      width: 100%;\n      height: 3px;\n      background: $white;\n      content: '';\n      transition: transform 0.35s;\n      transform: translate3d(-100%, 0, 0);\n    }\n  }\n\n  p {\n    padding: 1em 0;\n    opacity: 0;\n    transition: opacity 0.35s, transform 0.35s;\n    transform: translate3d(100%, 0, 0);\n  }\n}\n\n/*---------------*/\n/***** Zoe *****/\n/*---------------*/\nfigure.effect-zoe {\n  figcaption {\n    top: auto;\n    bottom: 0;\n    padding: 1em;\n    height: 3.75em;\n    background: $white;\n    color: $body-color;\n    transition: transform 0.35s;\n    transform: translate3d(0, 100%, 0);\n  }\n\n  h2 {\n    float: left;\n    transition: transform 0.35s;\n  