$black, 0.5) !important;\n      }\n      &.swiper-button-disabled{\n        display: none;\n      }\n    }\n  }\n}\n","@import \"../core/variables/variables\";\n@import \"../core/variables/app-variables\";\n\n// Knowledge base\n.kb-wrapper {\n  .kb-search {\n    .kb-search-input {\n      @include media-breakpoint-down(md) {\n        width: 75% !important;\n      }\n      @include media-breakpoint-down(xs) {\n        width: 100% !important;\n      }\n      input {\n        background-color: $white;\n      }\n      button {\n        right: 0.4rem;\n        top: 0.3rem;\n        @include media-breakpoint-down(xs) {\n          padding: 0.65rem 1.25rem;\n        }\n      }\n    }\n  }\n  .kb-content {\n    .kb-content-info i {\n      font-size: 2.3rem;\n    }\n  }\n}\n\n\n// Knowledge Categories & Question\n.kb-categories, .kb-question {\n  .kb-ellipsis {\n    display: -webkit-box;\n    -webkit-line-clamp: 3;\n    -webkit-box-orient: vertical;\n    overflow: hidden;\n    text-overflow: ellipsis;\n  }\n  .kb-effect {\n    transition: all 0.3s ease;\n    &:hover {\n      color: $primary !important;\n      margin-left: 0.5rem;\n    }\n  }\n  .kb-categories-text {\n    color: $gray-600;\n  }\n  // knowledge-base-categories & knowledge-base-question sidebar\n  @include media-breakpoint-down(sm) {\n    .kb-sidebar {\n      height: 100vh;\n      width: 260px;\n      background-color: $white;\n      position: absolute;\n      transform: translateX(-115%);\n      transition: all .3s ease;\n      z-index: 12;\n      left: 1rem;\n      top: 0;\n      padding: 1.1rem 0 0 1.2rem;\n      &.show {\n        transform: translateX(0%);\n      }\n      // sidebar close icon\n      .kb-close-icon {\n        position: absolute;\n        top: 0.9rem;\n        right: 0.9rem;\n      }\n    }\n    // knowledge base categories & question overlay\n    .kb-overlay {\n      top: 0;\n      left: 0;\n      right: 0;\n      bottom: 0;\n      position: absolute;\n      z-index: 11;\n      visibility: hidden;\n      &.show {\n        visibility: visible;\n        transition: all .3s ease;\n        background-color: rgba($black, 0.6);\n      }\n    }\n  }\n  @include media-breakpoint-down(xs) {\n    .kb-sidebar {\n      width: 250px;\n    }\n  }\n}\n","@import \"../core/variables/variables\";\n@import \"../core/variables/app-variables\";\n\n.page-user-profile {\n  .user-profile-images {\n    position: relative;\n\n    // user timeline image positioning\n    .user-timeline-image {\n      min-height: 150px;\n      object-fit: cover;\n    }\n\n    // user profile image positioning\n    .user-profile-image {\n      position: absolute;\n      left: 20px;\n      bottom: -65px;\n      box-shadow: 0px 0px 20px 0px rgba($black, 0.41);\n    }\n  }\n\n  // user profile text positioning\n  .user-profile-text {\n    position: absolute;\n    bottom: 105px;\n    left: 190px;\n    color: $white;\n\n    .profile-text-color {\n      color: $white;\n    }\n  }\n\n  // user profile buttons positioning\n  .user-profile-buttons {\n    padding-left: 11.25rem;\n  }\n\n  // profile info and posts\n  .profile-info-posts {\n    .profile-post {\n      .profile-post-img {\n        max-height: 300px;\n        object-fit: cover;\n      }\n    }\n  }\n\n  // user profile stories\n  .user-profile-stories-image {\n    max-height: 300px\n  }\n\n  .user-profile-stories {\n    &.swiper-container {\n      .swiper-slide {\n        width: auto !important;\n        cursor: pointer;\n\n        .user-swiper-text {\n          color: $white;\n          position: absolute;\n          bottom: 15px;\n          left: 10px;\n        }\n      }\n    }\n  }\n}\n\n@media(max-width: 576px) {\n  .user-profile-images {\n    // user profile image\n    .user-profile-image {\n      height: 100px;\n      width: 100px;\n      top: 40%;\n      left: 50% !important;\n      margin-left: -45px;\n      margin-top: 30px;\n    }\n  }\n\n  // user profile text\n  .user-profile-text {\n    top: 50%;\n    left: 50% !important;\n    margin-left: -60px;\n    margin-top: 3rem;\n    color: $headings-color !important;\n    text-align: center !important;\n\n    .profile-text-color {\n      color: $headings-color !important;\n    }\n  }\n\n  // user profile buttons\n  .user-profile-buttons {\n    padding-left: 0 !important;\n    padding-top: 6rem;\n  }\n}\n","// Coming Soon\n.card-coming-soon {\n  .getting-started {\n    display: flex;\n    flex-wrap: wrap;\n    justify-content: center;\n    font-size: 2.4rem;\n    font-weight: 500;\n  }\n}\n\n@media (max-width: 576px) {\n  .card-coming-soon {\n    top: -12px;\n    img {\n      width: 150px;\n    }\n  }\n}\n","@import \"../core/colors/palette-variables\";\n\n.cd-horizontal-timeline {\n  opacity: 0;\n  margin: 2em auto;\n  transition: opacity 0.2s;\n  &::before {\n    // never visible - this is used in jQuery to check the current MQ\n    content: \"mobile\";\n    display: none;\n  }\n\n  &.loaded {\n    // show the timeline after events position has been set (using JavaScript)\n    opacity: 1;\n  }\n\n  .timeline {\n    position: relative;\n    height: 100px;\n    width: 90%;\n    margin: 0 auto;\n    &:before,\n    &:after {\n      display: none;\n    }\n    ul,\n    ol {\n      li {\n        list-style: none;\n      }\n    }\n\n    .events-wrapper {\n      position: relative;\n      height: 100%;\n      margin: 0 40px;\n      overflow: hidden;\n      .events {\n        // this is the primary lighten-4 line timeline\n        position: absolute;\n        z-index: 1;\n        left: 0;\n        top: 49px;\n        height: 2px;\n        // width will be set using JavaScript\n        background: map-get($primary-color, lighten-4);\n        transition: transform 0.4s;\n        .filling-line {\n          // this is used to create the green line filling the timeline\n          position: absolute;\n          z-index: 1;\n          left: 0;\n          top: 0;\n          height: 100%;\n          width: 100%;\n          background-color: theme-color(primary);\n          transform: scale(0);\n          transform-origin: left center;\n          transition: transform 0.3s;\n        }\n      }\n    }\n  }\n\n\n  .events a {\n    position: absolute;\n    bottom: 0;\n    z-index: 2;\n    text-align: center;\n    padding-bottom: 15px;\n    color: $body-color;\n    &::after {\n      // this is used to create the event spot/circle\n      content: \"\";\n      position: absolute;\n      @include center(x);\n      bottom: -5px;\n      height: 12px;\n      width: 12px;\n      border-radius: 50%;\n      border: 2px solid map-get($primary-color, lighten-4);\n      background-color: $white;\n      transition: background-color 0.3s, border-color 0.3s;\n    }\n    &:hover::after {\n      background-color: theme-color(primary) !important;\n      border-color: theme-color(primary);\n    }\n\n    &.selected {\n      pointer-events: none;\n      &::after {\n        background-color: theme-color(primary);\n        border-color: theme-color(primary);\n      }\n    }\n\n    &.older-event::after {\n      border-color: theme-color(primary);\n    }\n  }\n  @media only screen and (min-width: 1100px) {\n    margin: 4rem auto;\n    &::before {\n      content: \"desktop\";\n    }\n  }\n\n  .events-content {\n    position: relative;\n    width: 100%;\n    margin: 2rem 0;\n    overflow: hidden;\n    transition: height 0.4s;\n\n    li {\n      list-style: none;\n      position: absolute;\n      z-index: 1;\n      width: 100%;\n      left: 0;\n      top: 0;\n      transform: translateX(-100%);\n      transition: all 0.25s ease;\n      padding: 0 5%;\n      opacity: 0;\n      animation-duration: 0.4s;\n      animation-timing-function: ease-in-out;\n\n      &.selected {\n        // visible event content\n        position: relative;\n        z-index: 2;\n        opacity: 1;\n        transform: translateX(0);\n      }\n      &.enter-right,\n      &.leave-right {\n        animation-name: cd-enter-right;\n      }\n      &.enter-left,\n      &.leave-left {\n        animation-name: cd-enter-left;\n      }\n      &.leave-right,\n      &.leave-left {\n        animation-direction: reverse;\n      }\n    }\n\n    li > * {\n      margin: 0 auto;\n    }\n    p {\n      line-height: 1.6;\n    }\n  }\n}\n\n.cd-timeline-navigation a {\n  // these are the left/right arrows to navigate the timeline\n  position: absolute;\n  z-index: 1;\n  height: 34px;\n  width: 34px;\n  border-radius: 50%;\n  border: 2px solid map-get($primary-color, lighten-4);\n  background-color: $white;\n  // replace text with an icon\n  overflow: hidden;\n  text-indent: 100%;\n  white-space: nowrap;\n  transition: border-color 0.3s;\n  &::after {\n    // arrow icon\n    content: \"\";\n    position: absolute;\n    height: 16px;\n    width: 16px;\n    @include center;\n    background: url(../../img/svg/cd-arrow.svg) no-repeat 0 0;\n  }\n  &.prev {\n    top: 60%;\n    left: 25px;\n    transform: translateY(-50%) rotate(180deg);\n  }\n  &.next {\n    top: 43%;\n    right: 8px;\n  }\n  &:hover {\n    border-color: theme-color(primary);\n  }\n  &.inactive {\n    cursor: not-allowed;\n    &::after {\n      background-position: 0 -16px;\n    }\n    &:hover {\n      border-color: map-get($primary-color, lighten-4);\n    }\n  }\n}\n\n\n@-webkit-keyframes cd-enter-right {\n  0% {\n    opacity: 0;\n    -webkit-transform: translateX(100%);\n  }\n  100% {\n    opacity: 1;\n    -webkit-transform: translateX(0%);\n  }\n}\n@-moz-keyframes cd-enter-right {\n  0% {\n    opacity: 0;\n    -moz-transform: translateX(100%);\n  }\n  100% {\n    opacity: 1;\n    -moz-transform: translateX(0%);\n  }\n}\n@keyframes cd-enter-right {\n  0% {\n    opacity: 0;\n    transform: translateX(100%);\n  }\n  100% {\n    opacity: 1;\n    transform: translateX(0%);\n  }\n}\n@-webkit-keyframes cd-enter-left {\n  0% {\n    opacity: 0;\n    -webkit-transform: translateX(-100%);\n  }\n  100% {\n    opacity: 1;\n    -webkit-transform: translateX(0%);\n  }\n}\n@-moz-keyframes cd-enter-left {\n  0% {\n    opacity: 0;\n    -moz-transform: translateX(-100%);\n  }\n  100% {\n    opacity: 1;\n    -moz-transform: translateX(0%);\n  }\n}\n@keyframes cd-enter-left {\n  0% {\n    opacity: 0;\n    transform: translateX(-100%);\n  }\n  100% {\n    opacity: 1;\n    transform: translateX(0%);\n  }\n}\n","// center vertically and/or horizontally an absolute positioned element\n\n@mixin center($xy: xy) {\n  @if $xy == xy {\n    left: 50%;\n    top: 50%;\n    bottom: auto;\n    right: auto;\n    transform: translate(-50%, -50%);\n    transition: all 0.25s ease;\n  } @else if $xy == x {\n    left: 50%;\n    right: auto;\n    transform: translateX(-50%);\n    transition: all 0.25s ease;\n  } @else if $xy == y {\n    top: 50%;\n    bottom: auto;\n    transform: translateY(-50%);\n    transition: all 0.25s ease;\n  }\n}\n","@import \"../core/colors/palette-variables\";\n\n// Timeline\n.timeline {\n  position: relative;\n  margin: 0;\n  padding: 10px 0;\n  &:after {\n    content: \"\";\n    display: table;\n    clear: both;\n  }\n  &:before {\n    content: \"\";\n    display: table;\n  }\n  & > li:nth-child(even) {\n    float: left;\n    clear: left;\n  }\n  & > li:nth-child(odd) {\n    float: right;\n    clear: right;\n  }\n\n  .timeline-line {\n    display: inline-block;\n    position: absolute;\n    top: 0;\n    bottom: 0;\n    width: 2px;\n    background-color: $border-color;\n    transform: translate(-50%, 0);\n  }\n\n  .timeline-group {\n    display: block;\n    position: relative;\n    text-align: center;\n    float: none !important;\n    z-index: 1;\n  }\n\n  .timeline-item {\n    position: relative;\n    display: inline-block;\n    line-height: inherit;\n\n    .timeline-card {\n      .progress {\n        .progress-bar {\n          border-radius: 0;\n        }\n      }\n    }\n\n    & > .timeline-badge {\n      position: absolute;\n      top: 12px;\n      z-index: 1;\n\n      & > span {\n        cursor: pointer;\n        display: inline-block;\n        width: 40px;\n        height: 40px;\n        border-radius: 50%;\n        text-align: center;\n        transition: all ease 0.3s;\n\n        &:active,\n        &:focus,\n        &:hover {\n          transform: scale(1.1);\n        }\n\n        i {\n          font-size: 1.4rem;\n          line-height: 40px;\n        }\n      }\n    }\n  }\n}\n\n\n//--------------Timeline Center-----------------\n//----------------------------------------------\n.timeline-center {\n  .timeline-line {\n    left: 50%;\n  }\n\n  .timeline-item {\n    width: 50%;\n    padding: 0 50px 35px;\n\n    &:nth-child(even) {\n      padding: 0px 35px 30px 0px;\n\n      &:after {\n        content: \"\";\n        position: absolute;\n        right: 14px;\n        top: 32px;\n        width: 1.4rem;\n        z-index: 0;\n        border: 1px dashed $border-color;\n      }\n      & > .timeline-badge {\n        right: -20px;\n      }\n    }\n\n    &:nth-child(odd) {\n      padding: 0px 0px 30px 35px;\n\n      &:after {\n        content: \"\";\n        position: absolute;\n        left: 20px;\n        top: 32px;\n        width: 1rem;\n        z-index: 0;\n        border: 1px dashed map-get($grey-color, lighten-2);\n      }\n      & > .timeline-badge {\n        left: -20px;\n      }\n    }\n\n    &.center-block {\n      width: 100%;\n      margin-top: 5px;\n      padding: 0 0 30px !important;\n      & > .timeline-badge {\n        top: -28px;\n        right: auto !important;\n        width: 100%;\n        text-align: center;\n      }\n      &:after {\n        display: none;\n      }\n    }\n  }\n}\n\n\n//--------------Timeline Right------------------\n//----------------------------------------------\n.timeline-right {\n  .timeline-line {\n    right: 12px;\n  }\n\n  .timeline-group {\n    text-align: right;\n  }\n\n  .timeline-item {\n    width: 100%;\n    padding: 0px 3.2rem 2.333333rem 0px;\n    &:after {\n      content: \"\";\n      position: absolute;\n      right: 32px;\n      top: 32px;\n      width: 1rem;\n      z-index: 0;\n      border: 1px dashed $border-color;\n    }\n\n    & > .timeline-badge {\n      right: -8px;\n    }\n\n    &.right-block {\n      &:after {\n        right: 15px;\n        width: 2.2rem;\n      }\n    }\n  }\n}\n\n\n//--------------Timeline Left-------------------\n//----------------------------------------------\n.timeline-left {\n  .timeline-line {\n    left: 12px;\n  }\n\n  .timeline-group {\n    text-align: left;\n  }\n\n  .timeline-item {\n    width: 100%;\n    padding: 0 0 2.333333rem 3.2rem;\n\n    &:after {\n      content: \"\";\n      position: absolute;\n      left: 32px;\n      top: 32px;\n      width: 1rem;\n      z-index: 0;\n      border: 1px dashed $border-color;\n    }\n\n    & > .timeline-badge {\n      left: -8px;\n    }\n\n    &.left-block {\n      &:after {\n        left: 13px;\n        width: 2.3rem;\n      }\n    }\n  }\n}\n\n\n\n\n@media (max-width: 991px) {\n\n  .timeline-center {\n    .timeline-item {\n      display: block !important;\n      padding: 20px 0 20px !important;\n      width: 100% !important;\n      margin-top: 0 !important;\n      float: none !important;\n      clear: both !important;\n\n      &:after,\n      &:before {\n        display: none;\n      }\n\n      & > .timeline-badge {\n        top: -12px !important;\n        right: auto !important;\n        left: auto !important;\n        width: 100%;\n        text-align: center;\n      }\n      .bg-transparent {\n        background-color: $body-bg !important;\n      }\n    }\n  }\n}\n\n@media (max-width: 575px) {\n  .timeline-wrapper {\n    padding: 0;\n  }\n}\n","@import \"../core/variables/variables\";\n@import \"../core/variables/app-variables\";\n@import \"../core/colors/palette-variables\";\n\n// Email application style\n.email-application {\n  position: relative;\n  border-radius: $border-radius;\n  height: calc(var(--vh, 1vh)*100 - 7.55rem);\n\n  // For Horizontal layout\n  .horizontal-layout & {\n    margin-top: 1rem;\n\n    &,\n    .email-app-sidebar {\n      height: calc(var(