portant;\n            }\n          }\n        }\n      }\n    }\n  }\n\n  .compose-new-mail-sidebar {\n    height: calc(100vh - 7.5rem);\n    width: 23rem;\n    border-radius: 0;\n    background-color: $white;\n    position: absolute;\n    z-index: 8;\n    right: 0;\n    bottom: 0;\n    transform: translateX(100%);\n    transition: all 0.4s ease-in-out;\n    -webkit-transition: all 0.4s ease-in-out;\n    -moz-transition: all 0.4s ease-in-out;\n    -o-transition: all 0.4s ease-in-out;\n    -ms-transition: all 0.4s ease-in-out;\n\n    &.show {\n      transform: translateX(0);\n    }\n\n    .compose-sidebar-close-icon {\n      position: absolute;\n      right: 1rem;\n      top: 0.6rem;\n      cursor: pointer;\n      font-size: 1.25rem;\n      z-index: 1;\n    }\n\n    .ql-editor {\n      min-height: 5rem;\n    }\n\n    [class*='ql'] {\n      border: 0;\n    }\n\n    @include media-breakpoint-down(xs) {\n      width: 18rem;\n    }\n  }\n\n  .email-app-content {\n    padding-right: 1rem;\n    height: 100%;\n\n    .email-search-box {\n      background-color: $white;\n    }\n  }\n\n  // Email list\n  .email-app-list-mails {\n    position: relative;\n    padding: 0px;\n    width: 100%;\n    overflow-y: hidden;\n    background-color: $white;\n    height: calc(var(--vh, 1vh)*100 - 13.5rem);\n\n    .email-app-list {\n      position: relative;\n      height: 100%;\n\n      p {\n        margin-bottom: 0.2rem;\n      }\n\n      .ps__rail-x,\n      .ps__rail-y {\n        z-index: 2;\n      }\n    }\n  }\n\n  .email-app-mail-content {\n    float: left;\n    z-index: 1;\n    width: calc(100% - 320px);\n    position: relative;\n    padding: 0px;\n    background-color: $white;\n    height: calc(var(--vh, 1vh)*100 - 13.5rem);\n    transition: all 0.4s ease-in-out;\n    -webkit-transition: all 0.4s ease-in-out;\n    -moz-transition: all 0.4s ease-in-out;\n    -o-transition: all 0.4s ease-in-out;\n    -ms-transition: all 0.4s ease-in-out;\n  }\n\n  .email-app-mail-content-detail {\n    position: relative;\n    height: calc(var(--vh, 1vh)*100 - 13.5rem);\n    overflow-y: auto;\n\n    .opened-email {\n      background: $body-bg;\n    }\n  }\n\n\n  .email-app-content-wrapper {\n    overflow-y: scroll;\n    height: calc(100% - 30px);\n\n    .email-app-options,\n    .email-app-title {\n      border-bottom: 1px solid $border-color;\n    }\n  }\n\n  .users-list-padding {\n    .list-group-item-text {\n      .float-right {\n        i.ft-star {\n          color: map-get($grey-color, lighten-1);\n        }\n      }\n    }\n\n    .email-list-text {\n      color: map-get($grey-color, darken-2);\n    }\n\n    .selected-email {\n      background-color: map-get($grey-color, lighten-3);\n      border: none;\n      border-right: 3px solid map-get($primary-color, base) !important;\n\n      &:first-child,\n      &:last-child {\n        border-radius: 0;\n      }\n    }\n  }\n}\n\n@include media-breakpoint-down(lg) {\n  .horizontal-layout .email-application {\n\n    &,\n    .email-app-sidebar,\n    .compose-new-mail-sidebar {\n      height: calc(var(--vh, 1vh)*100 - 8.5rem);\n    }\n  }\n\n  .email-app-sidebar {\n    transform: translateX(-110%);\n    transition: transform 0.4s;\n\n    &.show {\n      transform: translateX(0%);\n      z-index: 3;\n    }\n  }\n\n  .email-app-content {\n    padding-left: 1rem;\n  }\n}\n\n@include media-breakpoint-up(md) {\n  .email-application {\n    .email-app-sidebar {\n      left: 0;\n    }\n\n    .email-app-list-mails {\n      width: 319px;\n      border-right: 1px solid $border-color;\n      float: left;\n    }\n  }\n}\n\n@include media-breakpoint-up(xl) {\n  .email-application {\n    .email-app-content {\n      margin-left: 16rem;\n    }\n\n    // For Horizontal layout\n    .horizontal-layout & {\n\n      .email-app-sidebar,\n      .compose-new-mail-sidebar {\n        height: calc(var(--vh, 1vh)*100 - 11.98rem);\n      }\n\n      .email-app-list-mails,\n      .email-app-mail-content,\n      .email-app-mail-content-detail {\n       