color: map-get($grey-color, base);\n    }\n  }\n\n  // Chat\n  .chat-application {\n    .chat-list-text {\n      color: map-get($grey-color, base);\n    }\n  }\n\n  // Calendar\n  a.fc-more {\n    color: $white;\n  }\n\n  // Sidebar for above 1199px\n  @media (min-width: 1199px) {\n    .app-sidebar {\n      background: transparent !important;\n    }\n  }\n\n  // Sidebar for below 1199px\n  @media (max-width: 1199px) {\n    .app-sidebar {\n      background: rgba($black, 0.8) !important;\n      // transition: all 0.5s ease !important;\n    }\n  }\n\n  // app overlay for transparent layout\n  .app-content-overlay {\n    background-color: rgba($black, 0.7);\n  }\n\n  @include media-breakpoint-down(lg) {\n\n    // email app sidebar bg-color for transparent layout\n    .email-app-sidebar {\n      background-color: rgba($black, 0.9) !important;\n    }\n  }\n\n  @include media-breakpoint-down(sm) {\n\n    // chat app sidebar bg-color for transparent layout\n    .chat-sidebar {\n      background-color: rgba($black, 0.9) !important;\n    }\n  }\n\n  // Exclusive for Customizer - transparent\n  .customizer {\n\n    .sb-color-options,\n    .sb-options,\n    .sb-bg-img,\n    .toggle-sb-bg-img {\n      display: none !important;\n    }\n\n    .tl-color-option,\n    .cz-tl-bg-color,\n    .tl-bg-img {\n      display: block !important;\n    }\n  }\n\n  // Knowledge Categories & Question pages\n  .kb-categories-text {\n    color: $gray-500;\n  }\n\n  .kb-overlay.show {\n    background-color: rgba($black, 0.7);\n  }\n}\n\n// anchor tag for transparent layout is written outside the condition.\n// If this was to be written inside the condition, then the path for the anchor tag would be too long and it would create problems.\n.layout-transparent a {\n  color: map-get($primary-color, lighten-4);\n\n  &:hover {\n    color: darken(map-get($primary-color, lighten-4), 5%);\n  }\n}\n\n// Heading text colors\nh1,\nh2,\nh3,\nh4,\nh5,\nh6,\n.h1,\n.h2,\n.h3,\n.h4,\n.h5,\n.h6,\n.content-header,\n.table th {\n  @include transparent-style() {\n    color: $tl-headings-color;\n  }\n\n  @include dark-style() {\n    color: $dl-headings-color;\n  }\n}\n\n// Text colors\n.content-sub-header,\n.table td {\n  @include transparent-style() {\n    color: $tl-text-color;\n  }\n\n  @include dark-style() {\n    color: $dl-text-color;\n  }\n}\n\n.blockquote-footer,\n.text-muted {\n  @include transparent-style() {\n    color: $tl-text-muted-color !important;\n  }\n\n  @include dark-style() {\n    color: $dl-text-muted-color !important;\n  }\n}\n\n// disabled color\n.disabled {\n  @include transparent-style() {\n    color: $tl-disabled-color;\n  }\n\n  @include dark-style() {\n    color: $dl-disabled-color;\n  }\n}\n\n.logo-text {\n  @include transparent-style() {\n    color: $tl-headings-color;\n  }\n\n  @include dark-style() {\n    color: $dl-headings-color;\n  }\n}\n\n// Border colors\n.border {\n  @include transparent-style() {\n    border-color: $tl-border-color !important;\n  }\n\n  @include dark-style() {\n    border-color: $dl-border-color !important;\n  }\n}\n\n.border-top {\n  @include transparent-style() {\n    border-top-color: $tl-border-color !important;\n  }\n\n  @include dark-style() {\n    border-top-color: $dl-border-color !important;\n  }\n}\n\n.border-right {\n  @include transparent-style() {\n    border-right-color: $tl-border-color !important;\n  }\n\n  @include dark-style() {\n    border-right-color: $dl-border-color !important;\n  }\n}\n\n.border-bottom {\n  @include transparent-style() {\n    border-bottom-color: $tl-border-color !important;\n  }\n\n  @include dark-style() {\n    border-bottom-color: $dl-border-color !important;\n  }\n}\n\n.border-left {\n  @include transparent-style() {\n    border-left-color: $tl-border-color !important;\n  }\n\n  @include dark-style() {\n    border-left-color: $dl-border-color !important;\n  }\n}\n\n// hr color\nhr {\n  @include transparent-style() {\n    border-color: $tl-border-color !important;\n  }\n\n  @include dark-style() {\n    border-color: $dl-border-color;\n  }\n}\n\n// code and pre (tags)\ncode {\n  @include dark-style() {\n    background-color: $bg-color-dark;\n  }\n}\n\npre {\n  @include transparent-style() {\n    background: $tl-card-bg-color;\n  }\n\n  @include dark-style() {\n    background: $bg-color-dark;\n  }\n\n  code {\n    @include transparent-style() {\n      color: $tl-text-color;\n    }\n\n    @include dark-style() {\n      color: $dl-text-color;\n    }\n\n    &[class*=\"language-\"] {\n      @include transparent-style() {\n        background-color: transparent !important;\n      }\n    }\n  }\n}\n\n// Tables and DataTables\n.table {\n\n  thead,\n  tfoot {\n\n    th,\n    td {\n      @include transparent-style() {\n        background-color: $tl-card-bg-color;\n        border-color: $tl-border-color;\n      }\n\n      @include dark-style() {\n        background-color: $bg-color-dark;\n        border-color: $dl-border-color;\n      }\n    }\n  }\n\n  th,\n  td {\n    @include transparent-style() {\n      border-top-color: $tl-border-color;\n    }\n\n    @include dark-style() {\n      border-top-color: $dl-border-color;\n    }\n  }\n\n  &.table-bordered {\n    @include transparent-style() {\n      border-color: $tl-border-color;\n    }\n\n    @include dark-style() {\n      border-color: $dl-border-color;\n    }\n\n    th,\n    td {\n      @include transparent-style() {\n        border-color: $tl-border-color;\n      }\n\n      @include dark-style() {\n        border-color: $dl-border-color;\n      }\n    }\n  }\n\n  &.table-striped {\n    tbody {\n      tr:nth-of-type(odd) {\n        @include transparent-style() {\n          background-color: rgba(map-get($grey-color, darken-4), 0.1);\n        }\n\n        @include dark-style() {\n          background-color: map-get($grey-color, darken-4);\n        }\n      }\n    }\n  }\n\n  &.dataTable {\n    tr.group {\n      @include transparent-style() {\n        background-color: rgba(map-get($grey-color, darken-3), 0.2);\n      }\n\n      @include dark-style() {\n        background-color: map-get($grey-color, darken-3);\n      }\n    }\n  }\n}\n\n// Cards\n.card:not([class*=\"gradient-\"]) {\n  @include transparent-style() {\n    background: $tl-card-bg-color;\n  }\n\n  @include dark-style() {\n    background: $dl-bg-color;\n  }\n}\n\n// Dropdown\n.dropdown-menu {\n  @include transparent-style() {\n    background-color: rgba($bg-color-dark, 0.92);\n  }\n\n  @include dark-style() {\n    background-color: $bg-color-dark;\n  }\n\n  .dropdown-item:not(:active) {\n    @include transparent-style() {\n      color: $tl-text-color;\n    }\n\n    @include dark-style() {\n      color: $dl-text-color;\n    }\n  }\n}\n\n// Media Object\n.media-bordered {\n  .media {\n    @include transparent-style() {\n      border-top-color: $tl-border-color;\n    }\n\n    @include dark-style() {\n      border-top-color: $dl-border-color;\n    }\n  }\n}\n\n// Pagination\n.pagination {\n  .page-item {\n    .page-link {\n      @include transparent-style() {\n        background-color: $tl-card-bg-color;\n        border-color: $tl-card-bg-color;\n        color: $tl-text-color;\n      }\n\n      @include dark-style() {\n        background-color: $dl-bg-color;\n        border-color: $dl-border-color;\n      }\n    }\n\n    &.disabled {\n      .page-link {\n        @include transparent-style() {\n          color: map-get($grey-color, darken-1);\n        }\n\n        @include dark-style() {\n          color: map-get($grey-color, darken-1);\n        }\n      }\n    }\n  }\n}\n\n// Progress\n.progress {\n  @include transparent-style() {\n    background: $tl-card-bg-color;\n  }\n\n  @include dark-style() {\n    background: $bg-color-dark;\n  }\n}\n\n// Modal\n.modal-content {\n  @include transparent-style() {\n    background: rgba($bg-color-dark, 0.92);\n  }\n\n  @include dark-style() {\n    background: transparent;\n  }\n\n  .modal-header {\n    @include transparent-style() {\n      background: transparent;\n      