ansparent-style() {\n          border-right-color: $tl-border-color;\n        }\n\n        @include dark-style() {\n          border-right-color: $dl-border-color;\n        }\n      }\n    }\n  }\n}\n\n// Tabs\n.nav-tabs {\n  @include transparent-style() {\n    border-bottom-color: $tl-border-color;\n  }\n\n  @include dark-style() {\n    border-bottom-color: $dl-border-color;\n  }\n}\n\n.nav-vertical {\n  .nav-tabs {\n    &.nav-left {\n      .nav-item .nav-link {\n        @include transparent-style() {\n          border-right: 1px solid $tl-border-color;\n        }\n\n        @include dark-style() {\n          border-right: 1px solid $dl-border-color;\n        }\n      }\n    }\n\n    &.nav-right {\n      .nav-item .nav-link {\n        @include transparent-style() {\n          border-left: 1px solid $tl-border-color;\n        }\n\n        @include dark-style() {\n          border-left: 1px solid $dl-border-color;\n        }\n      }\n    }\n  }\n}\n\n// Navbar\n.navbar {\n\n  .navbar-nav .nav-item,\n  .nav-link,\n  .navbar-toggle {\n    @include transparent-style() {\n      color: $tl-headings-color;\n    }\n\n    @include dark-style() {\n      color: $dl-headings-color;\n    }\n  }\n}\n\n//----------- Forms -----------\n// form control\n.form-control {\n  @include transparent-style() {\n    border-color: $tl-border-color;\n    color: $tl-text-color;\n  }\n\n  @include dark-style() {\n    border-color: $dl-border-color;\n    color: $dl-text-color;\n  }\n\n  // placeholder\n  &::placeholder {\n    @include transparent-style() {\n      color: $tl-text-muted-color;\n    }\n\n    @include dark-style() {\n      color: $dl-text-muted-color;\n    }\n  }\n\n  &:disabled,\n  &[readonly] {\n    @include transparent-style() {\n      background-color: $tl-card-bg-color;\n    }\n\n    @include dark-style() {\n      background-color: $bg-color-dark;\n    }\n  }\n\n  &:focus {\n    @include transparent-style() {\n      border-color: map-get($primary-color, base);\n    }\n\n    @in