  background-color: $tl-card-bg-color;\n      color: $tl-text-color;\n    }\n\n    @include dark-style() {\n      background-color: $dl-bg-color;\n      color: $dl-text-color;\n    }\n  }\n}\n\n// Search page\n.search-wrapper {\n\n  .page-search-input input,\n  .video-result-swiper .swiper-slide {\n    @include transparent-style() {\n      background-color: $tl-card-bg-color;\n      color: $tl-text-color;\n    }\n\n    @include dark-style() {\n      background-color: $dl-bg-color;\n      color: $dl-text-color;\n    }\n  }\n}\n\n// Knowledge Base page\n.kb-wrapper {\n  .kb-search-input input {\n    @include transparent-style() {\n      background-color: $tl-card-bg-color;\n      color: $tl-text-color;\n    }\n\n    @include dark-style() {\n      background-color: $dl-bg-color;\n      color: $dl-text-color;\n    }\n  }\n}\n\n// Knowledge Categories & Question pages\n@include media-breakpoint-down(sm) {\n  .kb-sidebar {\n    @include transparent-style() {\n      background-color: rgba($black, 0.9);\n    }\n\n    @include dark-style() {\n      background-color: $dl-bg-color;\n    }\n  }\n}\n\n//--------------- Horizontal timeline ---------------\n.timeline {\n  .events li a {\n    @include transparent-style() {\n      color: $tl-text-color !important;\n    }\n\n    @include dark-style() {\n      color: $dl-text-color !important;\n    }\n\n    &:not(.selected):after {\n      @include dark-style() {\n        background-color: $dl-bg-color;\n      }\n    }\n  }\n\n  .timeline-line {\n    @include transparent-style() {\n      background-color: $tl-border-color;\n    }\n\n    @include dark-style() {\n      background-color: $dl-border-color;\n    }\n\n    &.tline-t-none {\n      @include transparent-style() {\n        background-color: transparent;\n      }\n    }\n  }\n\n  .timeline-item {\n    &:after {\n      @include transparent-style() {\n        border-color: $tl-border-color;\n      }\n\n      @include dark-style() {\n        border-color: $dl-border-color;\n      }\n    }\n  }\n\n  .cd-timeline-navigation {\n    a {\n      @include dark-style() {\n        background-color: $dl-bg-color;\n      }\n    }\n  }\n}\n\n@media (max-width: 991px) {\n  .timeline-center {\n    .timeline {\n      .timeline-line {\n        @include transparent-style() {\n          background-color: transparent;\n        }\n      }\n\n      .timeline-item {\n        .bg-transparent {\n          @include transparent-style() {\n            background-color: transparent !important;\n          }\n\n          @include dark-style() {\n            background-color: $bg-color-dark !important;\n          }\n        }\n      }\n    }\n  }\n}\n\n// Apex Charts\n.apexcharts-legend-text {\n  @include transparent-style() {\n    color: $tl-text-color !important;\n  }\n\n  @include dark-style() {\n    color: $dl-text-color !important;\n  }\n}\n\n\n\n// css for both dark and transparent layouts\nbody {\n  &.layout-dark {\n\n    // Drop Down\n    .dropdown-menu {\n      border-color: $dl-border-color;\n\n      .dropdown-header {\n        color: $white;\n      }\n\n      .dropdown-item {\n\n        &:hover,\n        &:focus {\n          background-color: $dl-bg-color !important;\n          // background-color: $dl-border-color !important;\n        }\n      }\n\n      .dropdown-divider {\n        border-top-color: $dl-border-color;\n      }\n\n      .arrow:after,\n      .arrow-left:after,\n      .arrow-right:after {\n        border-right-color: $bg-color-dark;\n      }\n    }\n\n    // Pagination\n    .pagination {\n      .page-item {\n        &.active {\n          .page-link {\n            background-color: $component-active-bg !important;\n            border-color: $component-active-bg !important;\n          }\n        }\n      }\n    }\n\n    // Modal\n    .modal-content {\n      .close {\n        color: $white;\n      }\n    }\n\n    // List Group\n    .list-group {\n      .list-group-item {\n        background-color: inherit;\n\n        &.active {\n          background-color: map-get($primary-color, base);\n          border-color: map-get($primary-color, base) !important;\n        }\n      }\n    }\n\n    // Nav Tabs\n    .nav-tabs {\n      .nav-item {\n        &.dropdown.show {\n          a.nav-link.dropdown-toggle {\n            color: $white;\n          }\n        }\n      }\n\n      &.nav-right,\n      &.nav-left {\n        .nav-item {\n          a.nav-link {\n            border-radius: 0;\n          }\n        }\n      }\n    }\n\n    // select\n    select {\n      option {\n        background-color: $dl-bg-color;\n      }\n    }\n\n    // select2\n    .select2.select2-container {\n      .select2-selection--multiple {\n        .select2-selection__rendered {\n          .select2-search__field {\n            color: inherit;\n          }\n        }\n      }\n    }\n\n    .select2-container {\n      .select2-dropdown {\n        background-color: $dl-bg-color;\n\n        .select2-search__field {\n          background-color: $dl-bg-color;\n          border-color: $dl-border-color;\n          color: inherit;\n        }\n      }\n\n      .select2-results {\n        .select2-results__options {\n          .select2-results__option {\n            .select2-results__group {\n              background-color: $bg-color-dark;\n            }\n          }\n        }\n      }\n    }\n\n    // Tooltip\n    .tooltip {\n      color: $dl-headings-color !important;\n    }\n\n    // Sweet Alerts\n    .swal2-modal {\n      h2 {\n        color: $body-color !important;\n      }\n    }\n\n    // Drag and Drop\n    .heading-elements {\n      position: absolute;\n      top: 1.25rem;\n      right: 1.25rem;\n\n      ul {\n        li {\n          span {\n            background: $dl-bg-color;\n          }\n        }\n      }\n    }\n\n    // media player page css\n    .media-wrapper {\n      .plyr--audio {\n        .plyr__controls {\n          .plyr__time--current.plyr__time {\n            color: $white;\n          }\n        }\n      }\n    }\n\n    // treeview page css\n    .bootstraptreeview {\n      .colorful-treeview {\n        .list-group {\n          .list-group-item {\n            background-color: theme-color(primary);\n\n            &:hover {\n              background-color: map-get($primary-color, lighten-1);\n            }\n          }\n        }\n      }\n\n      .override-treeview {\n        .list-group {\n          .list-group-item {\n            background-color: map-get($warning-color, lighten-4);\n\n            &:hover {\n              background-color: map-get($warning-color, lighten-3);\n            }\n          }\n        }\n      }\n    }\n\n    // miscellaneous > context-menu\n    .context-menu-list {\n      border: none;\n      box-shadow: $dl-card-shadow;\n\n      .context-menu-item {\n        border: none;\n\n        &.context-menu-hover {\n          background-color: map-get($primary-color, base) !important;\n          color: $white;\n        }\n      }\n    }\n\n    // Editor\n    .ql-toolbar.ql-snow {\n      .ql-formats {\n        .ql-picker {\n          color: $white !important;\n\n          &-options {\n            color: $body-color;\n          }\n        }\n\n        svg {\n          stroke: transparent;\n\n          .ql-stroke {\n            stroke: $white;\n          }\n\n          .ql-fill {\n            fill: $white;\n          }\n        }\n\n        .ql-active {\n          border: 1px solid $white;\n        }\n      }\n    }\n\n    // navbar search\n    .nav-search {\n      .search-input {\n        &.open {\n          background: $dl-bg-color !important;\n\n          .input {\n            color: $dl-text-color;\n          }\n\n          .search-list {\n            background: $bg-color-dark;\n\n            li {\n              &.current_item {\n                background: $dl-bg-color;\n              }\n\n              a {\n                color: $dl-text-color;\n              }\n            }\n          }\n        }\n      }\n    }\n\n    // Navbar notification\n    .notification-dropdown .media {\n      border-bottom-color: $dl-border-color !important;\n    }\n\n    // Customizer\n    .customizer {\n\n      .cz-bg-color,\n      .cz-bg-image,\n      .cz-tl-bg-color {\n        .selected {\n          border-color: $dl-bg-color;\n        }\n      }\n    }\n\n    // Charts & SVG\n    svg {\n      stroke: $dl-headings-color;\n    }\n\n    .apexcharts-toolbar {\n      svg {\n        stroke: none;\n      }\n    }\n\n    .ct-grid {\n      stroke: rgba($white, 0.22);\n    }\n\n    // users view page css\n    .users-view {\n      .table-borderless {\n        td {\n          border: none;\n        }\n      }\n    }\n  }\n}\n\n//-------------Horizontal Layout-------------------\n\n@include dark-only() {\n  &.horizontal-layout {\n    &.page-scrolled {\n      &.navbar-sticky {\n        nav.header-navbar {\n          background-color: $dl-bg-color;\n          border-bottom-color: rgba($dl-border-color, 0.4);\n        }\n      }\n\n      div.header-navbar {\n        box-shadow: $dl-card-shadow;\n      }\n    }\n\n    nav.header-navbar {\n      background-color: $bg-color-dark;\n      border-bottom-color: $dl-border-color;\n    }\n\n    div.navbar-horizontal {\n      background-color: $bg-color-dark;\n\n      li.nav-item.show {\n        .dropdown-menu .dropdown-item {\n\n          &:active,\n          &:focus {\n            background-color: $dl-bg-color !important;\n            color: $dl-text-color !important;\n          }\n        }\n\n        li.active.dropdown-submenu {\n          background-color: $dl-bg-color !important;\n          color: $dl-text-color !important;\n        }\n\n        li {\n          &.show {\n            background-color: $dl-bg-color !important;\n            color: $dl-text-color !important;\n          }\n\n          &:active {\n            &.dropdown-item {\n              background-color: $dl-bg-color !important;\n              color: $dl-text-color !important;\n            }\n          }\n        }\n      }\n\n      .main-menu-content>ul>li {\n        &:hover:not(.active) {\n          background-color: $dl-bg-color;\n        }\n      }\n    }\n  }\n}\n\n@include transparent-only() {\n  &.horizontal-layout {\n    &.page-scrolled {\n      div.header-navbar {\n        @include card-shadow();\n      }\n    }\n\n    nav.header-navbar {\n      background-color: transparent;\n      border-bottom-color: $tl-border-color;\n    }\n\n    div.navbar-horizontal {\n      background-color: transparent;\n\n      li.nav-item {\n        &:hover:not(.active) {\n          background-color: $tl-card-bg-color;\n        }\n\n        &.show {\n          li {\n            &.show {\n              background-color: $bg-color-dark !important;\n              color: $dl-text-color !important;\n            }\n          }\n        }\n      }\n    }\n  }\n}\n","/*=========================================================================================\n\tFile Name: horizontal-menu.scss\n\tDescription: A classic horizontal menu for easy navingation & support all devices.\n\tIt support light & dark version, filpped layout, right side icons, borders menu for\n\titem seperation.\n\t----------------------------------------------------------------------------------------\n\tItem Name: Apex - HTML 5 Bootstrap Admin Template\n\tAuthor: PIXINVENT\n\tAuthor URL: http://www.themeforest.net/user/pixinvent\n==========================================================================================*/\n\n// Core variables and mixins\n@import \"../variables/variables\";\n@import \"../variables/app-variables\";\n@import \"../colors/palette-variables\";\n@import \"../mixins/shadows\";\n\n// Horizontal Menu\n//=========================\n.horizontal-menu {\n\n  &.navbar-sticky,\n  & {\n\n    // .header-navbar {\n    //   position: fixed;\n    //   top: 0;\n    //   width: 100%;\n    // }\n    &:not(.auth-page):not(.navbar-static) {\n      .main-panel {\n        margin-top: 7.49rem;\n      }\n    }\n  }\n\n  &.navbar-sticky {\n    &.page-scrolled:not(.layout-dark) {\n      nav.header-navbar {\n        background-color: $white;\n      }\n    }\n\n    nav.header-navbar {\n      position: fixed;\n      top: 0;\n      width: 100%;\n    }\n  }\n\n  &.navbar-static {\n    .header-navbar.navbar-sticky {\n      position: sticky;\n      top: 0;\n    }\n\n    &.navbar-scrolled {\n      .wrapper {\n        padding-top: 52px;\n      }\n\n      .header-navbar.navbar-sticky {\n        position: fixed;\n      }\n    }\n  }\n\n  &.page-scrolled:not(.layout-dark) {\n    div.header-navbar {\n      @include card-shadow();\n    }\n  }\n\n  &:not([class*=\"application\"]) .main-content {\n    min-height: calc((var(--vh, 1vh) * 100) - 10.93rem);\n  }\n\n  &[class*=\"application\"] .main-content {\n    min-height: calc((var(--vh, 1vh) * 100) - 12rem);\n  }\n\n  // Horizontal Menu Styling\n  .navbar-horizontal {\n    padding-left: 2rem;\n    position: unset;\n\n    //dropdown menu animation remove\n    .dropdown {\n      i {\n        margin-right: .5rem;\n      }\n\n      .dropdown-menu {\n        transition: none;\n        margin-top: 1px;\n        min-width: 16rem;\n        animation: .8s cubic-bezier(.25, .8, .25, 1) 0s normal forwards 1 fadein;\n\n        .dropdown-submenu .dropdown-menu {\n          top: 8px;\n          margin-left: 1px;\n        }\n\n        >li {\n          &:first-child {\n            border-top-left-radius: $border-radius;\n            border-top-right-radius: $border-radius;\n          }\n\n          &:last-child {\n            border-bottom-left-radius: $border-radius;\n            border-bottom-right-radius: $border-radius;\n          }\n        }\n      }\n    }\n\n    .main-menu-content {\n      padding-left: 0.5rem;\n\n      .nav-link {\n        padding: 0.5rem .75rem .45rem;\n        font-weight: 500;\n        color: $body-color;\n\n        &:not(.dropdown-toggle) {\n          display: flex;\n          align-items: center;\n        }\n\n        i {\n          margin-right: .5rem;\n        }\n      }\n\n      >ul>li {\n        margin-right: 0.5rem;\n\n        &.active:not(.dropdown-submenu) {\n          background-color: map-get($primary-color, lighten-4);\n          border-radius: $border-radius;\n\n          >a {\n            color: map-get($primary-color, darken-1);\n          }\n        }\n\n        &:hover:not(.active) {\n          background-color: $menu-active-bg;\n          border-radius: $border-radius;\n        }\n\n        &.show {\n          .dropdown-menu {\n            animation: .8s cubic-bezier(.25, .8, .25, 1) 0s normal forwards 1 fadein;\n\n            li.show {\n              background-color: $menu-active-bg;\n              color: $body-color;\n            }\n          }\n\n          li.active:not(.dropdown-submenu) {\n            background-color: map-get($primary-color, lighten-4) !important;\n\n            a {\n              color: map-get($primary-color, base) !important;\n            }\n\n            >.dropdown-item:hover,\n            &:hover,\n            >.dropdown-item:hover:active,\n            &:hover:active {\n              background-color: map-get($primary-color, lighten-4) !important;\n              color: map-get($primary-color, base) !important;\n            }\n\n          }\n        }\n\n        &.dropdown {\n          i {\n            font-size: 1.1rem;\n\n            &.submenu-icon {\n              font-size: 0.8rem;\n            }\n          }\n        }\n      }\n    }\n  }\n\n  &:not(.layout-dark) {\n    .navbar-horizontal .main-menu-content>ul>li.show {\n      .dropdown-menu {\n        animation: .8s cubic-bezier(.25, .8, .25, 1) 0s normal forwards 1 fadein;\n\n        li.show {\n          background-color: $menu-active-bg;\n          color: $body-color;\n        }\n\n        .dropdown-item {\n\n          &.active,\n          &:active,\n          &:focus {\n            background-color: $menu-active-bg !important;\n            color: $body-color;\n            border-radius: 0;\n          }\n        }\n      }\n\n      l