ader {\n        @include menu-navbar-width(auto);\n      }\n    }\n  }\n\n  .wrapper {\n    top: 4rem;\n  }\n\n  .app-sidebar {\n    .navigation {\n      li a {\n        letter-spacing: 0.6px;\n        font-size: .95rem;\n      }\n    }\n  }\n\n  // Main menu expanded\n  .main-menu {\n    opacity: 0;\n    transform: translate3d(0, 0, 0);\n    transition: opacity .25s, transform .25s;\n    width: $menu-expanded-width;\n    left: -($menu-expanded-width);\n\n\n    .navigation {\n      .navigation-header {\n        .feather.icon-minus {\n          display: none;\n        }\n      }\n\n      >li {\n        >a {\n          >i {\n            font-size: 1.2rem;\n            margin-right: 12px;\n            float: left;\n\n            &:before {\n              transition: 200ms ease all;\n            }\n          }\n        }\n      }\n\n      li {\n        @include vertical-menu-has-sub-arrow(1rem);\n      }\n    }\n\n    .main-menu-footer {\n      bottom: 55px;\n    }\n\n    .main-menu-footer {\n      width: $menu-expanded-width;\n    }\n\n    &.menu-fixed {\n      position: fixed;\n      top: 4rem;\n      height: calc(100% - 4rem);\n    }\n  }\n\n  &.menu-open {\n    .main-menu {\n      opacity: 1;\n      transform: translate3d($menu-expanded-width, 0, 0);\n      transition: opacity .25s, transform .25s;\n      z-index: 998;\n    }\n  }\n\n  &.menu-hide {\n    .main-menu {\n      @extend .main-menu;\n    }\n  }\n\n  // Flipped menu open\n  &.menu-flipped {\n\n    // Main menu hidden on flipped\n    .main-menu {\n      right: -($menu-expanded-width);\n      left: inherit;\n    }\n\n    //Navbar flipped\n    .navbar {\n      .navbar-container {\n        @include vertical-flipped-menu($menu-expanded-width);\n      }\n\n      .navbar-header {\n        float: right;\n      }\n    }\n\n    &.menu-open {\n      .main-menu {\n        transform: translate3d(-$menu-expanded-width, 0, 0);\n      }\n    }\n  }\n\n  &.navbar-sticky {\n    .main-panel {\n      margin-top: 0;\n    }\n  